import React from 'react';

import { TextInput } from '../../TextInput';
import type { Props } from './types';

const CreditCardName = (
  {
    value = '',
    className,
    id,
    name = '',
    placeholder,
    label = '',
    info,
    helptext,
    error = false,
    errorMessage,
    readOnly = false,
    disabled = false,
    onChange,
    onBlur,
    onFocus,
  }: Props,
  ref: any
) => {
  const customFilter = (e: string) => {
    const newString = e
      .replace(/[^a-zA-Z\s]*$/g, '')
      .replace(/(\..*)\./g, '$1');

    return newString;
  };
  return (
    <TextInput
      value={String(value)}
      id={id}
      className={className}
      name={name}
      label={label}
      placeholder={placeholder}
      info={info}
      customFilter={customFilter}
      helptext={helptext}
      error={error}
      errorMessage={errorMessage}
      readOnly={readOnly}
      disabled={disabled}
      onChange={onChange}
      onBlur={onBlur}
      onFocus={onFocus}
      ref={ref}
    />
  );
};

export default React.forwardRef(CreditCardName);
