import React, { useState } from 'react';

import classNames from 'classnames';
import zxcvbn from 'zxcvbn';

import { Button, Telicon } from '../../GeneralComponents';
import { Label } from '../Label';
import type { Props, InputFocusEvent } from './types';
import './password.scss';

const formatInputValue = (
  value: string,
  maxLength?: number,
  filterFn?: (value: string) => string
) => {
  let formattedValue = value || '';

  if (maxLength) {
    formattedValue = formattedValue.slice(0, maxLength);
  }

  if (filterFn) {
    formattedValue = filterFn(formattedValue);
  }

  return formattedValue;
};

const Password = (
  {
    className,
    label = 'Password',
    value: controlledValue,
    id,
    name = '',
    placeholder,
    info,
    error = false,
    errorMessage,
    disabled = false,
    score = false,
    customFilter,
    onBlur,
    onChange,
    onFocus,
    maxLength,
  }: Props,
  ref: any
) => {
  const isInteractivityDisabled = disabled;
  const [innerValue, setInnerValue] = useState('');
  const value = controlledValue || innerValue;
  const formattedValue = formatInputValue(value, maxLength, customFilter);
  const [visibility, setVisibility] = useState(false);
  const [currentScore, setCurrentScore] = useState({
    textScore: '',
    emojiScore: '',
  });

  const scorePassword = (parsedValue: string) => {
    if (parsedValue.length > 0) {
      const zxcvbnScore = zxcvbn(parsedValue);
      let scoreRes;

      switch (zxcvbnScore.score) {
        case 4:
          scoreRes = {
            textScore: 'Strong',
            emojiScore: '',
          };
          setCurrentScore(scoreRes);
          break;
        case 3:
          scoreRes = {
            textScore: 'Good',
            emojiScore: '',
          };
          setCurrentScore(scoreRes);
          break;
        case 2:
          scoreRes = {
            textScore: 'Okay',
            emojiScore: '',
          };
          setCurrentScore(scoreRes);
          break;
        case 1:
          scoreRes = {
            textScore: 'Weak',
            emojiScore: '',
          };
          setCurrentScore(scoreRes);
          break;
        default:
          scoreRes = {
            textScore: 'Bad',
            emojiScore: '',
          };
          setCurrentScore(scoreRes);
          break;
      }

      return scoreRes;
    }
    const scoreRes = {
      textScore: '',
      emojiScore: '',
    };
    setCurrentScore(scoreRes);

    return scoreRes;
  };

  const onChangeEvent = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = event.target.value;
    setInnerValue(newValue);

    if (!onChange || isInteractivityDisabled) return;

    const newFormattedValue = formatInputValue(
      newValue,
      maxLength,
      customFilter
    );

    const newScore = scorePassword(newFormattedValue);
    setCurrentScore(newScore);

    onChange({
      label,
      name,
      id,
      score: newScore,
      value: newFormattedValue,
      event,
    });
  };

  const onBlurEvent = (event: InputFocusEvent<HTMLInputElement>) => {
    if (!onBlur || isInteractivityDisabled) return;

    onBlur({
      label,
      name,
      id,
      score: currentScore,
      value: event.target.value,
      event,
    });
  };

  const onFocusEvent = (event: InputFocusEvent<HTMLInputElement>) => {
    if (!onFocus || isInteractivityDisabled) return;

    onFocus({
      label,
      name,
      id,
      score: currentScore,
      value: event.target.value,
      event,
    });
  };

  return (
    <div
      className={classNames('sds_Password', {
        sds_Password_Error: error,
        sds_Password_Disabled: disabled,
        sds_Password_InternalIcon: error || score,
        [String(className)]: className,
      })}>
      {label && (
        <Label info={info} htmlFor={id}>
          {label}
        </Label>
      )}
      <div className="sds_Password_Box">
        <div className="sds_Password_InputBox">
          <input
            type={visibility ? 'text' : 'password'}
            value={formattedValue}
            placeholder={placeholder}
            id={id}
            name={name}
            disabled={disabled}
            onChange={onChangeEvent}
            onBlur={onBlurEvent}
            onFocus={onFocusEvent}
            ref={ref}
          />
          {error && <Telicon name="warning--octagon" size="Default" />}
          {score && !error && (
            <span className="sds_Password_Emoji">
              {currentScore.emojiScore}
            </span>
          )}
        </div>
        <Button
          type="Base"
          size="Default"
          icon={visibility ? 'eye' : 'eye-slash'}
          disabled={disabled}
          onClick={() => {
            setVisibility((prevState) => !prevState);
          }}
        />
      </div>
      {error && errorMessage !== '' ? (
        <label className="sds_Password_HelpText">{errorMessage}</label>
      ) : (
        score &&
        !error && (
          <label className="sds_Password_HelpText">
            {currentScore.textScore}
          </label>
        )
      )}
    </div>
  );
};

export default React.forwardRef(Password);
