import React, { MouseEvent } from 'react';

import classNames from 'classnames';

import { Loader } from '../Loader';
import { Telicon } from '../Telicon';
import type { Props } from './types';

import './button.scss';

const Button = (
  {
    className,
    children,
    type = 'Base',
    size = 'Default',
    icon,
    iconPosition = 'Left',
    disabled,
    selected,
    loader,
    'data-tip': dataTip,
    'data-for': dataFor,
    buttonMenu = false,
    submit,
    reset,
    onClick,
    onFocus,
    onMouseOver,
    onMouseOut,
    onBlur,
    customData,
  }: Props,
  ref: any
) => {
  const click = (e: MouseEvent<HTMLButtonElement>): void => {
    if (disabled || loader) {
      e.preventDefault();
    } else if (onClick) {
      onClick(e);
    }
  };

  const getButtonType = () => {
    if (submit && !reset) return 'submit';

    if (reset && !submit) return 'reset';

    return 'button';
  };

  return (
    <button
      className={classNames('sds_Button', {
        sds_Button_Active: !disabled,
        sds_Button_Disabled: disabled,
        [`sds_Button_Size_${size}`]: true,
        sds_Button_Icon_Right: iconPosition === 'Right',
        sds_Button_Icon_Only: !children && icon,
        sds_Button_Loader: loader,
        sds_Button_Selected: selected,
        [`sds_Button_Type_${type}`]: true,
        sds_ButtonMenu: buttonMenu,
        [String(className)]: className,
      })}
      onClick={click}
      onFocus={onFocus}
      onMouseOver={onMouseOver}
      onMouseOut={onMouseOut}
      onBlur={onBlur}
      {...(dataFor && { 'data-for': dataFor })}
      {...(dataTip && { 'data-tip': dataTip })}
      {...(customData && { 'custom-data': customData })}
      type={getButtonType()}
      ref={ref}>
      {icon && !loader && (
        <Telicon name={icon} size={size === 'Large' ? 'Medium' : 'Default'} />
      )}
      {loader && <Loader size={size === 'Large' ? 'Medium' : 'Default'} />}
      {buttonMenu && iconPosition === 'Left' && (
        <Telicon
          className="sds_ButtonMenu_Icon"
          name="carets-sortable"
          size={size === 'Large' ? 'Medium' : 'Default'}
        />
      )}
      {children && children}
    </button>
  );
};

export default React.forwardRef(Button);
