import { useState, useEffect } from 'react';

export const useMenu = (
  autoposition: boolean,
  menuIndicatorRef: any,
  filteringClick: (e: any) => void
) => {
  const [menuPosition, setMenuPosition] = useState({
    x: true,
    y: true,
  });

  function getPosition () {
    if (autoposition && menuIndicatorRef.current) {
      const menuHeight =
        menuIndicatorRef.current.nextElementSibling.getBoundingClientRect()
          .height;
      const menuWidth =
        menuIndicatorRef.current.nextElementSibling.getBoundingClientRect()
          .width;
      const menuIndicator = menuIndicatorRef.current.getBoundingClientRect();
      const menuIndicatorXPosition = menuIndicator.x + menuWidth + 10;
      const menuIndicatorYPosition = menuIndicator.y + 56 + menuHeight;

      const windowWidth = window.innerWidth;
      const windowHeight = window.innerHeight;

      setMenuPosition({
        x: menuIndicatorXPosition <= windowWidth,
        y: menuIndicatorYPosition <= windowHeight,
      });
    }
  }

  useEffect(() => {
    document.addEventListener('click', (e: any) => {
      getPosition();
      filteringClick(e);
    });

    if (autoposition) {
      window.addEventListener('scroll', getPosition, true);
    }

    return () => {
      document.removeEventListener('click', (e: any) => {
        filteringClick(e);
      });

      if (autoposition) {
        window.removeEventListener('scroll', getPosition, true);
      }
    };
  }, []);

  return { menuPosition };
};
