import React, { useState, useEffect, useRef } from 'react';

import classNames from 'classnames';

import { Label } from '../Label';
import { NumberInput } from '../NumberInput';
import type { Props } from './types';
import './slider.scss';

const Slider = ({
  className,
  id,
  name = '',
  label = '',
  info,
  hasInput,
  value: defaultValue = 0,
  rangeVisibility = false,
  minValue = 0,
  maxValue = 100,
  disabled,
  error,
  errorMessage,
  onChange,
}: Props) => {
  const sliderRef = useRef<any>(null);
  const inputRef = useRef<any>(null);
  const [value, setValue] = useState(defaultValue);

  const rangeChange = (e: any) => {
    if (!disabled) {
      setValue(e.target.value);

      if (onChange) {
        onChange({
          label,
          id,
          name,
          value: e.target.value,
        });
      }
    }
  };

  const inputChange = (e: any) => {
    setValue(e.value === '' ? 0 : e.value);

    if (onChange) {
      onChange({
        id,
        name,
        value: e.value === '' ? '0' : e.value,
      });
    }
  };

  const setStyles = () => {
    const baseRange = maxValue - minValue;
    const baseValue = value - minValue;
    const percentage = (baseValue * 100) / baseRange;

    if (error) {
      inputRef.current.style.background = `linear-gradient(
        to right,
        var(--sds_slider_error_track_fill) ${percentage}%,
        var(--sds_slider_error_track_background) ${percentage}%
      )`;
    } else {
      inputRef.current.style.background = `linear-gradient(
        to right,
        var(--sds_slider_track_fill) ${percentage}%,
        var(--sds_slider_track_background) ${percentage}%
      )`;
    }
  };

  // Render in realtime the values of the component (Most used for storybook)
  useEffect(() => {
    if (defaultValue < minValue) {
      setValue(minValue);
    } else {
      setValue(defaultValue);
    }
  }, [defaultValue, minValue, maxValue]);

  // Applies the colors extracted from the theme and calculates the percentage of the filled area
  useEffect(() => {
    setStyles();
  }, [value, error]);

  return (
    <div
      className={classNames('sds_Slider', {
        sds_Slider_HasInput: hasInput,
        sds_Slider_RangeVisible: rangeVisibility,
        sds_Slider_Disabled: disabled,
        sds_Slider_Error: error,
        [String(className)]: className,
      })}
      ref={sliderRef}>
      {label && (
        <Label info={info} htmlFor={id}>
          {label}
        </Label>
      )}
      <div className="sds_Slider_Box">
        {rangeVisibility && <span>{minValue}</span>}
        <input
          id={!hasInput && !disabled ? id : ''}
          name={name}
          type="range"
          min={minValue}
          max={maxValue}
          value={value}
          onChange={rangeChange}
          ref={inputRef}
        />
        {rangeVisibility && <span>{maxValue}</span>}
        {hasInput && (
          <NumberInput
            value={value || 0}
            hideButtons
            id={!disabled ? id : ''}
            error={error}
            disabled={disabled}
            onChange={inputChange}
          />
        )}
      </div>
      {error && errorMessage && (
        <span className="sds_Slider_ErrorMessage">{errorMessage}</span>
      )}
    </div>
  );
};

export default React.memo(Slider);
