import React from 'react';

import { NumberInput } from '../../NumberInput';
import type { Props } from './types';

const CreditCardNumber = (
  {
    value = '',
    className,
    placeholder,
    id,
    name = '',
    label = '',
    info,
    helptext,
    error = false,
    errorMessage,
    readOnly = false,
    disabled = false,
    onChange,
    onBlur,
    onFocus,
  }: Props,
  ref: any
) => (
  <NumberInput
    className={className}
    id={id}
    placeholder={placeholder}
    name={name}
    label={label}
    hideButtons
    value={String(value)}
    info={info}
    helptext={helptext}
    error={error}
    errorMessage={errorMessage}
    readOnly={readOnly}
    disabled={disabled}
    onChange={onChange}
    onBlur={onBlur}
    onFocus={onFocus}
    ref={ref}
  />
);

export default React.forwardRef(CreditCardNumber);
