import React from 'react';

import classNames from 'classnames';

import { iconList } from './List';
import type { Props } from './types';
import './telicon.scss';

const Telicon = ({
  className,
  name,
  size = 'Default',
  'data-tip': dataTip,
  'data-for': dataFor,
  onClick,
}: Props) => {
  const click = () => {
    if (onClick) {
      onClick();
    }
  };

  return (
    <svg
      className={classNames('sds_Telicon', {
        sds_Telicon_Default: size === 'Default',
        sds_Telicon_Medium: size === 'Medium',
        sds_Telicon_Large: size === 'Large',
        [String(className)]: className,
      })}
      fill="currentColor"
      {...(dataFor && { 'data-for': dataFor })}
      {...(dataTip && { 'data-tip': dataTip })}
      onClick={click}>
      {iconList[name]}
      <use href={`#${name}`} />
    </svg>
  );
};

export default Telicon;
