import React, { useState } from 'react';

import classNames from 'classnames';

import { Button } from '../../GeneralComponents/Button';
import { Telicon } from '../../GeneralComponents/Telicon';
import { Label } from '../Label';
import type { InputFocusEvent, Props } from './types';
import './number-input.scss';

const parseValue = (value: string) =>
  value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');

const parseWholeNumberInput = (value: string) =>
  value.replace(/[^0-9]/g, '').replace(/(\..*)\./g, '$1');

const formatInputValue = (
  value: string,
  maxLength?: number,
  wholeNumbersOnly?: boolean,
  filterFn?: (value: string) => string
) => {
  let formattedValue = value || '';
  formattedValue = parseValue(formattedValue);

  if (maxLength) {
    formattedValue = formattedValue.slice(0, maxLength);
  }

  if (wholeNumbersOnly) {
    formattedValue = parseWholeNumberInput(formattedValue);
  }

  if (filterFn) {
    formattedValue = filterFn(formattedValue);
  }

  return formattedValue;
};

const NumberInput = (
  {
    value: controlledValue,
    className,
    id,
    name = '',
    placeholder,
    label = '',
    info,
    helptext,
    error = false,
    errorMessage,
    hideButtons = false,
    readOnly = false,
    disabled = false,
    customFilter,
    onChange,
    onBlur,
    onFocus,
    maxLength,
    wholeNumbersOnly,
  }: Props,
  ref: any
) => {
  const isInteractivityDisabled = disabled || readOnly;
  const [innerValue, setInnerValue] = useState('');
  const value = controlledValue || innerValue;
  const formattedValue = formatInputValue(
    String(value),
    maxLength,
    wholeNumbersOnly,
    customFilter
  );

  const onChangeEvent = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = event.target.value;
    setInnerValue(newValue);

    if (!onChange || isInteractivityDisabled) return;

    const newFormattedValue = formatInputValue(
      newValue,
      maxLength,
      wholeNumbersOnly,
      customFilter
    );

    onChange({
      label,
      name,
      id,
      value: newFormattedValue,
      event,
    });
  };

  const onBlurEvent = (event: InputFocusEvent<HTMLInputElement>) => {
    if (!onBlur || isInteractivityDisabled) return;

    onBlur({
      label,
      name,
      id,
      value: event.target.value,
      event,
    });
  };

  const onFocusEvent = (event: InputFocusEvent<HTMLInputElement>) => {
    if (!onFocus || isInteractivityDisabled) return;

    onFocus({
      label,
      name,
      id,
      value: event.target.value,
      event,
    });
  };

  const increase = () => {
    setInnerValue((prevValue) => {
      if (onChange) {
        onChange({
          label,
          name,
          id,
          value: String(Number(prevValue) + 1),
          event: undefined,
        });
      }

      return String(Number(prevValue) + 1);
    });
  };

  const decrease = () => {
    setInnerValue((prevValue) => {
      if (onChange) {
        onChange({
          label,
          name,
          id,
          value: String(Number(prevValue) - 1),
          event: undefined,
        });
      }

      return String(Number(prevValue) - 1);
    });
  };

  return (
    <div
      className={classNames('sds_NumberInput', {
        sds_NumberInput_Error: error,
        sds_NumberInput_ReadOnly: readOnly,
        sds_NumberInput_Disabled: disabled,
        sds_NumberInput_InternalIcon: readOnly || error,
        sds_NumberInput_NoButtons: hideButtons,
        [String(className)]: className,
      })}>
      {label && (
        <Label info={info} htmlFor={id}>
          {label}
        </Label>
      )}
      <div className="sds_NumberInput_InputBox">
        <input
          id={id}
          name={name}
          {...(!readOnly && !disabled && { id })}
          className="sds_NumberInput_Input"
          value={formattedValue}
          placeholder={placeholder}
          onChange={onChangeEvent}
          onBlur={onBlurEvent}
          onFocus={onFocusEvent}
          disabled={disabled}
          ref={ref}
        />
        {error && !readOnly && (
          <Telicon
            name="warning--octagon"
            size="Default"
            className="sds_NumberInput_Icon"
          />
        )}
        {readOnly && !error && (
          <Telicon
            name="lock"
            size="Default"
            className="sds_NumberInput_Icon"
          />
        )}
        {!hideButtons && (
          <div className="sds_NumberInput_Btns">
            <Button
              icon="minus"
              type="Base"
              onClick={decrease}
              disabled={disabled || readOnly}
            />
            <Button
              icon="plus"
              type="Base"
              onClick={increase}
              disabled={disabled || readOnly}
            />
          </div>
        )}
      </div>
      {error && errorMessage !== '' ? (
        <label className="sds_NumberInput_HelpText">{errorMessage}</label>
      ) : (
        helptext && (
          <label className="sds_NumberInput_HelpText">{helptext}</label>
        )
      )}
    </div>
  );
};

export default React.forwardRef(NumberInput);
