import React from 'react';

import uniqid from 'uniqid';

import { MenuOption } from '../MenuOption';

export const getContent = (
  childrenProp: any,
  noContentMessage: string,
  selectedItem: string,
  checkSelectedItems: boolean,
  removeSelectedItems: boolean,
  clickEvent: (e: any) => void
) => {
  let content: any[] = [];

  if (childrenProp) {
    const children = Array.isArray(childrenProp)
      ? childrenProp
      : [childrenProp];

    children.forEach((element) => {
      if (element.type === MenuOption) {
        if (selectedItem === '') {
          content = [
            ...content,
            React.cloneElement(element, {
              onClick: clickEvent,
              key: uniqid('sds_Option_'),
            }),
          ];
        } else if (removeSelectedItems && !checkSelectedItems) {
          if (element.props.value !== selectedItem) {
            content = [
              ...content,
              React.cloneElement(element, {
                onClick: clickEvent,
                key: uniqid('sds_Option_'),
              }),
            ];
          }
        } else if (!removeSelectedItems && checkSelectedItems) {
          if (element.props.value !== selectedItem) {
            content = [
              ...content,
              React.cloneElement(element, {
                onClick: clickEvent,
                key: uniqid('sds_Option_'),
              }),
            ];
          } else {
            content = [
              ...content,
              React.cloneElement(element, {
                onClick: clickEvent,
                className: 'sds_MenuOption_Checked',
                key: uniqid('sds_Option_'),
              }),
            ];
          }
        } else {
          content = [
            ...content,
            React.cloneElement(element, {
              onClick: clickEvent,
              key: uniqid('sds_Option_'),
            }),
          ];
        }
      } else {
        content = [
          ...content,
          React.cloneElement(element, {
            key: uniqid('sds_Option_'),
          }),
        ];
      }
    });
  } else {
    content = [
      <div
        className="sds_Option sds_Option_NoResult"
        key={uniqid('sds_Option_')}>
        {noContentMessage}
      </div>,
    ];
  }

  return content;
};
