import React from 'react';

import classNames from 'classnames';

import { Telicon } from '../../GeneralComponents/Telicon';
import { Tooltip } from '../../GeneralComponents/Tooltip';
import type { Props } from './types';
import './label.scss';

const Label = ({ className, children, info, htmlFor, onClick }: Props) => {
  const click = (e: any) => {
    if (onClick) {
      onClick(e);
    }
  };

  return (
    <div
      className={classNames('sds_Label', { [String(className)]: className })}>
      <label htmlFor={htmlFor} onClick={click}>
        {children}
      </label>
      {info && (
        <Tooltip message={info} position="Top">
          <Telicon name="question--circle" size="Default" />
        </Tooltip>
      )}
    </div>
  );
};

export default Label;
