import React, { useState } from 'react';

import classNames from 'classnames';

import { Telicon } from '../../GeneralComponents';
import { Label } from '../Label';
import type { Props, InputFocusEvent } from './types';
import './textarea.scss';

const formatInputValue = (
  value: string,
  maxLength?: number,
  filterFn?: (value: string) => string
) => {
  let formattedValue = value || '';

  if (maxLength) {
    formattedValue = formattedValue.slice(0, maxLength);
  }

  if (filterFn) {
    formattedValue = filterFn(formattedValue);
  }

  return formattedValue;
};

const Textarea = (
  {
    value: controlledValue,
    className,
    id,
    name = '',
    placeholder,
    label = '',
    info,
    helptext,
    maxLength,
    maxLengthCounter,
    error = false,
    errorMessage,
    readOnly = false,
    disabled,
    customFilter,
    onChange,
    onBlur,
    onFocus,
  }: Props,
  ref: any
) => {
  const isInteractivityDisabled = disabled || readOnly;
  const [innerValue, setInnerValue] = useState('');
  const value = controlledValue || innerValue;
  const formattedValue = formatInputValue(value, maxLength, customFilter);
  const [characterLength, setCharacterLength] = useState(formattedValue.length);

  const countCharacters = (textareaValue: string) => {
    if (maxLengthCounter) {
      const currentLenght = textareaValue.length;
      setCharacterLength(currentLenght);
    }
  };

  const onChangeEvent = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
    const newValue = event.target.value;
    setInnerValue(newValue);

    if (!onChange || isInteractivityDisabled) return;

    const newFormattedValue = formatInputValue(
      newValue,
      maxLength,
      customFilter
    );

    countCharacters(newFormattedValue);

    onChange({
      label,
      name,
      id,
      value: newFormattedValue,
      event,
    });
  };

  const onBlurEvent = (event: InputFocusEvent<HTMLTextAreaElement>) => {
    if (!onBlur || isInteractivityDisabled) return;

    onBlur({
      label,
      name,
      id,
      value: event.target.value,
      event,
    });
  };

  const onFocusEvent = (event: InputFocusEvent<HTMLTextAreaElement>) => {
    if (!onFocus || isInteractivityDisabled) return;

    onFocus({
      label,
      name,
      id,
      value: event.target.value,
      event,
    });
  };

  return (
    <div
      className={classNames('sds_Textarea', {
        sds_Textarea_Error: error,
        sds_Textarea_ReadOnly: readOnly,
        sds_Textarea_Disabled: disabled,
        sds_Textarea_InternalIcon: readOnly || error,
        [String(className)]: className,
      })}>
      {label && (
        <Label info={info} htmlFor={id}>
          {label}
        </Label>
      )}
      <div className="sds_Textarea_TextareaBox">
        <textarea
          id={id}
          name={name}
          {...(!readOnly && !disabled && { id })}
          className="sds_Textarea_Textarea"
          value={formattedValue}
          placeholder={placeholder}
          onChange={onChangeEvent}
          onBlur={onBlurEvent}
          onFocus={onFocusEvent}
          disabled={disabled}
          ref={ref}
        />
        {error && !readOnly && (
          <Telicon name="warning--octagon" size="Default" />
        )}
        {readOnly && !error && <Telicon name="lock" size="Default" />}
      </div>
      <div
        className={classNames('sds_HelpTextBox', {
          sds_HelpTextBoxDouble: maxLengthCounter && helptext !== '',
        })}>
        {error && errorMessage !== '' ? (
          <label className="sds_Textarea_HelpText sds_Textarea_HelpText_Message">
            {errorMessage}
          </label>
        ) : (
          helptext && (
            <label className="sds_Textarea_HelpText sds_Textarea_HelpText_Message">
              {helptext}
            </label>
          )
        )}
        {maxLengthCounter && (
          <label className="sds_Textarea_HelpText">{`${characterLength}/${maxLengthCounter}`}</label>
        )}
      </div>
    </div>
  );
};

export default React.forwardRef(Textarea);
