import React, { useState } from 'react';

import classNames from 'classnames';
import creditCardType from 'credit-card-type';

import { Telicon } from '../../../GeneralComponents/Telicon';
import { useUpdate } from '../../../hooks';
import { Label } from '../../Label';
import Amex from '../assets/CreditCard-Amex.svg';
import Discover from '../assets/CreditCard-Discover.svg';
import Mastercard from '../assets/CreditCard-Mastercard.svg';
import Visa from '../assets/CreditCard-Visa.svg';
import type { Props } from './types';
import './number-input.scss';

const parseValue = (value: string) => {
  const newString = value.replace(/[^0-9]/g, '').replace(/(\..*)\./g, '$1');
  return newString;
};

const CreditCardNumber = (
  {
    value: propValue,
    className,
    id,
    name = '',
    placeholder,
    label = '',
    info,
    helptext,
    error = false,
    errorMessage,
    readOnly = false,
    disabled = false,
    customFilter,
    onChange,
    onBlur,
    onFocus,
  }: Props,
  ref: any
) => {
  const parsingValue = (value: any): string | undefined => {
    let inputValue = value === undefined || value === null ? '' : String(value);
    inputValue = inputValue.trim();
    inputValue = parseValue(inputValue);

    const array = inputValue.split('');

    if (array.length > 4) {
      array.splice(4, 0, ' ');
    }

    if (array.length > 9) {
      array.splice(9, 0, ' ');
    }

    if (array.length > 14) {
      array.splice(14, 0, ' ');
    }

    let newString = '';

    array.forEach((letter: string) => {
      newString += letter;
    });

    inputValue = newString.slice(0, 19);

    if (customFilter) {
      inputValue = customFilter(inputValue);
    }

    return inputValue;
  };

  const [innerValue, setInnerValue] = useState(parsingValue(propValue));
  const [creditCard, setCreditCard] = useState('');

  const onChangeEvent = (event: any) => {
    const parsedValue = parsingValue(event.target.value);
    !disabled && !readOnly && setInnerValue(parsedValue);

    if (onChange && !disabled && !readOnly) {
      onChange({
        label,
        name,
        id,
        value: parsedValue,
        event,
      });
    }
  };

  const onBlurEvent = (event: any) => {
    if (onBlur && !disabled && !readOnly) {
      onBlur({
        label,
        name,
        id,
        value: event.target.value,
        event,
      });
    }
  };

  const onFocusEvent = (event: any) => {
    if (onFocus && !disabled && !readOnly) {
      onFocus({
        label,
        name,
        id,
        value: event.target.value,
        event,
      });
    }
  };

  useUpdate(() => {
    setInnerValue(parsingValue(propValue));

    if (onChange && !disabled && !readOnly) {
      onChange({
        label,
        name,
        id,
        value: parsingValue(
          propValue === undefined || propValue === null ? '' : propValue
        ),
        event: undefined,
      });
    }
  }, [propValue]);

  useUpdate(() => {
    if (innerValue) {
      const cardBrands = creditCardType(innerValue);
      if (cardBrands[0]) {
        setCreditCard(cardBrands[0].type);
      } else {
        setCreditCard('');
      }
    } else {
      setCreditCard('');
    }
  }, [innerValue]);

  return (
    <div
      className={classNames('sds_CreditCardNumber', {
        sds_CreditCardNumber_Error: error,
        sds_CreditCardNumber_ReadOnly: readOnly,
        sds_CreditCardNumber_Disabled: disabled,
        sds_CreditCardNumber_InternalIcon: readOnly || error,
        [String(className)]: className,
      })}>
      {label && (
        <Label info={info} htmlFor={id}>
          {label}
        </Label>
      )}
      <div className="sds_CreditCardNumber_InputBox">
        <input
          id={id}
          name={name}
          {...(!readOnly && !disabled && { id })}
          className="sds_CreditCardNumber_Input"
          value={innerValue}
          placeholder={placeholder}
          onChange={onChangeEvent}
          onBlur={onBlurEvent}
          onFocus={onFocusEvent}
          disabled={disabled}
          ref={ref}
        />
        {creditCard === 'visa' && (
          <img
            className="sds_CreditCardNumber_CardIcon"
            src={Visa}
            alt="Visa"
          />
        )}
        {creditCard === 'mastercard' && (
          <img
            className="sds_CreditCardNumber_CardIcon"
            src={Mastercard}
            alt="Visa"
          />
        )}
        {creditCard === 'american-express' && (
          <img
            className="sds_CreditCardNumber_CardIcon"
            src={Amex}
            alt="Visa"
          />
        )}
        {creditCard === 'discover' && (
          <img
            className="sds_CreditCardNumber_CardIcon"
            src={Discover}
            alt="Visa"
          />
        )}
        {error && !readOnly && (
          <Telicon
            name="warning--octagon"
            size="Default"
            className={classNames('sds_CreditCardNumber_Icon', {
              sds_CreditCardNumber_Icon_NextToCard: creditCard !== '',
            })}
          />
        )}
        {readOnly && !error && (
          <Telicon
            name="lock"
            size="Default"
            className={classNames('sds_CreditCardNumber_Icon', {
              sds_CreditCardNumber_Icon_NextToCard: creditCard !== '',
            })}
          />
        )}
      </div>
      {error && errorMessage !== '' ? (
        <label className="sds_CreditCardNumber_HelpText">{errorMessage}</label>
      ) : (
        helptext && (
          <label className="sds_CreditCardNumber_HelpText">{helptext}</label>
        )
      )}
    </div>
  );
};

export default React.forwardRef(CreditCardNumber);
