import React, { useState } from 'react';

import classNames from 'classnames';
import uniqid from 'uniqid';

import { Button } from '../../../GeneralComponents/Button';
import { Telicon } from '../../../GeneralComponents/Telicon';
import { Text } from '../../../GeneralComponents/Text';
import { useUpdate } from '../../../hooks';
import {
  singleDateRegExp,
  getCurrentDateInfo,
  getGeneralCalendarData,
  checkCurrentDay,
  checkSelectedDate,
  checkMinMaxDayConditionals,
  checkBtnConditional,
  checkDateRange,
  checkHoverDateRange,
  getRangeDateLimit,
  checkRangeLimit,
  checkYearBtnConditional,
  checkYearConditional,
  getInitialYearList,
  rangeSelectDay,
  selectionMenuFunctions,
  prevMonth,
  nextMonth,
  nextYearList,
  previousYearList,
  updateYear,
  getCalendarState,
} from '../helper';
import type { Props } from './calendarTypes';
import '../calendar.scss';
import './range-calendar.scss';

const Calendar = (
  {
    lang = 'en',
    className,
    show = false,
    onChange,
    startDate = '',
    endDate = '',
    maxDate,
    minDate,
    rangeLimit,
    selectionMenu,
    selectionMenuOptions = {
      today: true,
      yesterday: true,
      last7Days: true,
      last30Days: true,
      thisMonth: true,
      lastMonth: true,
    },
    selectionMenuOptionsText = {
      today: 'Today',
      yesterday: 'Yesterday',
      last7Days: 'Last 7 Days',
      last30Days: 'Last 30 Days',
      thisMonth: 'This Month',
      lastMonth: 'Last Month',
    },
  }: Props,
  ref: any
) => {
  // Getting General Info
  const { currentDateObj, currentDateString } = getCurrentDateInfo(lang);
  const { shortWeekDaysArray: weekDays, monthsArray: months } =
    getGeneralCalendarData(lang);

  // States
  const [selectedDates, setSelectedDates] = useState({
    startDate,
    endDate,
  });
  const [activeDate, setActiveDate] = useState(
    getCalendarState(
      startDate && startDate !== '' && singleDateRegExp.test(startDate)
        ? startDate
        : currentDateString,
      lang
    )
  );
  const [hoverDate, setHoverDate] = useState('');
  const [rangeDateLimit, setRangeDateLimit] = useState(
    startDate !== '' && rangeLimit
      ? getRangeDateLimit(startDate, rangeLimit)
      : ''
  );
  const [yearMenu, setYearMenu] = useState(false);
  const [yearList, setYearList] = useState<number[]>([]);
  const [activeSelector, setActiveSelector] = useState('');

  // Active Date Destructuring
  const {
    activeDateObj,
    activeMonthDays,
    activeLastMontDays,
    activeNextMonthDays,
  } = activeDate;

  // Functions to update values
  useUpdate(() => {
    rangeLimit && setRangeDateLimit(getRangeDateLimit(startDate, rangeLimit));

    setSelectedDates((prevState) => ({ ...prevState, startDate }));
  }, [startDate, rangeLimit]);

  useUpdate(() => {
    setSelectedDates((prevState) => ({ ...prevState, endDate }));
  }, [endDate]);

  useUpdate(() => {
    if (yearMenu) {
      if (yearList.length === 0) {
        setYearList(getInitialYearList(activeDateObj.getFullYear()));
      }
    }
  }, [yearMenu]);

  return (
    <div
      ref={ref}
      className={classNames(
        'sds_DatePicker_Calendar sds_DatePicker_RangeCalendar',
        {
          sds_DatePicker_Calendar_Hidden: !show,
          [String(className)]: className,
        }
      )}>
      {selectionMenu && (
        <div className="sds_DatePicker_Calendar_SelectionMenu">
          {selectionMenuOptions.today && (
            <div
              className={classNames(
                'sds_DatePicker_Calendar_SelectionMenu_Item',
                {
                  sds_DatePicker_Calendar_SelectionMenu_Item_Active:
                    activeSelector === 'today',
                }
              )}
              onClick={() =>
                selectionMenuFunctions(
                  'today',
                  currentDateObj,
                  setSelectedDates,
                  setActiveSelector,
                  onChange,
                  setActiveDate,
                  lang
                )
              }>
              <Text.span>{selectionMenuOptionsText.today}</Text.span>
            </div>
          )}
          {selectionMenuOptions.yesterday && (
            <div
              className={classNames(
                'sds_DatePicker_Calendar_SelectionMenu_Item',
                {
                  sds_DatePicker_Calendar_SelectionMenu_Item_Active:
                    activeSelector === 'yesterday',
                }
              )}
              onClick={() =>
                selectionMenuFunctions(
                  'yesterday',
                  currentDateObj,
                  setSelectedDates,
                  setActiveSelector,
                  onChange,
                  setActiveDate,
                  lang
                )
              }>
              <Text.span>{selectionMenuOptionsText.yesterday}</Text.span>
            </div>
          )}
          {selectionMenuOptions.last7Days && (
            <div
              className={classNames(
                'sds_DatePicker_Calendar_SelectionMenu_Item',
                {
                  sds_DatePicker_Calendar_SelectionMenu_Item_Active:
                    activeSelector === 'last7Days',
                }
              )}
              onClick={() =>
                selectionMenuFunctions(
                  'last7Days',
                  currentDateObj,
                  setSelectedDates,
                  setActiveSelector,
                  onChange,
                  setActiveDate,
                  lang
                )
              }>
              <Text.span>{selectionMenuOptionsText.last7Days}</Text.span>
            </div>
          )}
          {selectionMenuOptions.last30Days && (
            <div
              className={classNames(
                'sds_DatePicker_Calendar_SelectionMenu_Item',
                {
                  sds_DatePicker_Calendar_SelectionMenu_Item_Active:
                    activeSelector === 'last30Days',
                }
              )}
              onClick={() =>
                selectionMenuFunctions(
                  'last30Days',
                  currentDateObj,
                  setSelectedDates,
                  setActiveSelector,
                  onChange,
                  setActiveDate,
                  lang
                )
              }>
              <Text.span>{selectionMenuOptionsText.last30Days}</Text.span>
            </div>
          )}
          {selectionMenuOptions.thisMonth && (
            <div
              className={classNames(
                'sds_DatePicker_Calendar_SelectionMenu_Item',
                {
                  sds_DatePicker_Calendar_SelectionMenu_Item_Active:
                    activeSelector === 'thisMonth',
                }
              )}
              onClick={() =>
                selectionMenuFunctions(
                  'thisMonth',
                  currentDateObj,
                  setSelectedDates,
                  setActiveSelector,
                  onChange,
                  setActiveDate,
                  lang
                )
              }>
              <Text.span>{selectionMenuOptionsText.thisMonth}</Text.span>
            </div>
          )}
          {selectionMenuOptions.lastMonth && (
            <div
              className={classNames(
                'sds_DatePicker_Calendar_SelectionMenu_Item',
                {
                  sds_DatePicker_Calendar_SelectionMenu_Item_Active:
                    activeSelector === 'lastMonth',
                }
              )}
              onClick={() =>
                selectionMenuFunctions(
                  'lastMonth',
                  currentDateObj,
                  setSelectedDates,
                  setActiveSelector,
                  onChange,
                  setActiveDate,
                  lang
                )
              }>
              <Text.span>{selectionMenuOptionsText.lastMonth}</Text.span>
            </div>
          )}
        </div>
      )}
      <div
        className={classNames({
          sds_DatePicker_Calendar_Box_Active: selectionMenu,
        })}>
        <div className="sds_DatePicker_Calendar_Header">
          <Button
            icon="chevron-left"
            type="Ghost"
            onClick={() =>
              yearMenu
                ? previousYearList(yearList[0], setYearList)
                : prevMonth(activeDateObj, setActiveDate, lang)
            }
            disabled={
              yearMenu
                ? checkYearBtnConditional(yearList, maxDate, minDate)
                  .minDateValidation
                : minDate
                  ? checkBtnConditional('Min', minDate, activeDateObj)
                  : false
            }
          />
          <div>
            <Text.span>{months[activeDateObj.getMonth()]}</Text.span>
            <div
              className={classNames(
                'sds_DatePicker_Calendar_Header_YearSelection',
                {
                  sds_DatePicker_Calendar_Header_YearSelection_Active: yearMenu,
                }
              )}
              onClick={() => {
                setYearMenu((prevState) => !prevState);
              }}>
              <Text.span>{activeDateObj.getFullYear()}</Text.span>
              <Telicon name="carets-sortable" />
            </div>
          </div>
          <Button
            icon="chevron-right"
            type="Ghost"
            onClick={() =>
              yearMenu
                ? nextYearList(yearList[yearList.length - 1], setYearList)
                : nextMonth(activeDateObj, setActiveDate, lang)
            }
            disabled={
              yearMenu
                ? checkYearBtnConditional(yearList, maxDate, minDate)
                  .maxDateValidation
                : maxDate
                  ? checkBtnConditional('Max', maxDate, activeDateObj)
                  : false
            }
          />
        </div>
        {yearMenu ? (
          <div className="sds_DatePicker_Calendar_YearList">
            {yearList.map((yearItem) => {
              const yearValidation = checkYearConditional(
                yearItem,
                minDate,
                maxDate
              );

              return (
                <div
                  className={classNames(
                    'sds_DatePicker_Calendar_YearList_Year',
                    {
                      sds_DatePicker_Calendar_YearList_Year_Current:
                        yearItem === currentDateObj.getFullYear(),
                      sds_DatePicker_Calendar_YearList_Year_Disabled:
                        yearValidation,
                    }
                  )}
                  key={uniqid('sds_DatePicker_Calendar_YearList_Year_')}
                  onClick={() => {
                    updateYear(
                      yearItem,
                      yearValidation,
                      activeDateObj,
                      setActiveDate,
                      setYearMenu,
                      lang
                    );
                  }}>
                  <Text.span className="sds_DatePicker_Calendar_YearList_Year_Text">
                    {yearItem}
                  </Text.span>
                </div>
              );
            })}
          </div>
        ) : (
          <>
            <div className="sds_DatePicker_Calendar_DaysOfWeek">
              {weekDays.map((day: string) => (
                <div key={uniqid('sds_DatePicker_Calendar_DaysOfWeek_Day_')}>
                  <Text.span>{day}</Text.span>
                </div>
              ))}
            </div>
            <div className="sds_DatePicker_Calendar_daysOfMonth">
              {activeLastMontDays.map((day) => (
                <div
                  key={uniqid('sds_DatePicker_Calendar_daysOfMonth_Day_')}
                  className={classNames(
                    ' sds_DatePicker_Calendar_daysOfLastMonth_Day',
                    {
                      sds_DatePicker_Calendar_daysOfMonth_Day_CurrentDay:
                        checkCurrentDay(
                          day,
                          'lastMonth',
                          activeDateObj,
                          currentDateObj
                        ),
                      sds_DatePicker_Calendar_daysOfMonth_Day_SelectedDay:
                        checkSelectedDate(
                          day,
                          'lastMonth',
                          activeDateObj,
                          selectedDates.startDate
                        ) ||
                        checkSelectedDate(
                          day,
                          'lastMonth',
                          activeDateObj,
                          selectedDates.endDate
                        ),
                      sds_DatePicker_Calendar_daysOfMonth_Day_Range:
                        checkDateRange(
                          'lastMonth',
                          activeDateObj,
                          selectedDates.startDate,
                          selectedDates.endDate,
                          day
                        ),
                      sds_DatePicker_Calendar_daysOfMonth_Day_HoveredRange:
                        checkHoverDateRange(
                          'lastMonth',
                          activeDateObj,
                          selectedDates.startDate,
                          selectedDates.endDate,
                          hoverDate,
                          day
                        ),
                      sds_DatePicker_Calendar_daysOfMonth_Day_Disabled:
                        checkMinMaxDayConditionals(
                          day,
                          'lastMonth',
                          activeDateObj,
                          minDate,
                          maxDate
                        ) ||
                        checkRangeLimit(
                          day,
                          'lastMonth',
                          activeDateObj,
                          rangeDateLimit
                        ),
                    }
                  )}
                  onMouseOver={() => {
                    rangeSelectDay(
                      'hover',
                      'lastMonth',
                      day,
                      selectedDates,
                      setSelectedDates,
                      setHoverDate,
                      activeDateObj,
                      minDate,
                      maxDate,
                      onChange,
                      setActiveSelector
                    );
                  }}
                  onClick={() => {
                    rangeLimit
                      ? !checkRangeLimit(
                        day,
                        'lastMonth',
                        activeDateObj,
                        rangeDateLimit
                      ) &&
                        rangeSelectDay(
                          'select',
                          'lastMonth',
                          day,
                          selectedDates,
                          setSelectedDates,
                          setHoverDate,
                          activeDateObj,
                          minDate,
                          maxDate,
                          onChange,
                          setActiveSelector
                        )
                      : rangeSelectDay(
                        'select',
                        'lastMonth',
                        day,
                        selectedDates,
                        setSelectedDates,
                        setHoverDate,
                        activeDateObj,
                        minDate,
                        maxDate,
                        onChange,
                        setActiveSelector
                      );
                  }}>
                  <Text.span>{day}</Text.span>
                </div>
              ))}
              {activeMonthDays.map((day) => (
                <div
                  key={uniqid('sds_DatePicker_Calendar_daysOfMonth_Day_')}
                  className={classNames(
                    'sds_DatePicker_Calendar_daysOfMonth_Day',
                    {
                      sds_DatePicker_Calendar_daysOfMonth_Day_CurrentDay:
                        checkCurrentDay(
                          day,
                          'currentMonth',
                          activeDateObj,
                          currentDateObj
                        ),
                      sds_DatePicker_Calendar_daysOfMonth_Day_SelectedDay:
                        checkSelectedDate(
                          day,
                          'currentMonth',
                          activeDateObj,
                          selectedDates.startDate
                        ) ||
                        checkSelectedDate(
                          day,
                          'currentMonth',
                          activeDateObj,
                          selectedDates.endDate
                        ),
                      sds_DatePicker_Calendar_daysOfMonth_Day_Range:
                        checkDateRange(
                          'currentMonth',
                          activeDateObj,
                          selectedDates.startDate,
                          selectedDates.endDate,
                          day
                        ),
                      sds_DatePicker_Calendar_daysOfMonth_Day_HoveredRange:
                        checkHoverDateRange(
                          'currentMonth',
                          activeDateObj,
                          selectedDates.startDate,
                          selectedDates.endDate,
                          hoverDate,
                          day
                        ),
                      sds_DatePicker_Calendar_daysOfMonth_Day_Disabled:
                        checkMinMaxDayConditionals(
                          day,
                          'currentMonth',
                          activeDateObj,
                          minDate,
                          maxDate
                        ) ||
                        checkRangeLimit(
                          day,
                          'currentMonth',
                          activeDateObj,
                          rangeDateLimit
                        ),
                    }
                  )}
                  onMouseOver={() => {
                    rangeSelectDay(
                      'hover',
                      'currentMonth',
                      day,
                      selectedDates,
                      setSelectedDates,
                      setHoverDate,
                      activeDateObj,
                      minDate,
                      maxDate,
                      onChange,
                      setActiveSelector
                    );
                  }}
                  onClick={() => {
                    rangeLimit
                      ? !checkRangeLimit(
                        day,
                        'currentMonth',
                        activeDateObj,
                        rangeDateLimit
                      ) &&
                        rangeSelectDay(
                          'select',
                          'currentMonth',
                          day,
                          selectedDates,
                          setSelectedDates,
                          setHoverDate,
                          activeDateObj,
                          minDate,
                          maxDate,
                          onChange,
                          setActiveSelector
                        )
                      : rangeSelectDay(
                        'select',
                        'currentMonth',
                        day,
                        selectedDates,
                        setSelectedDates,
                        setHoverDate,
                        activeDateObj,
                        minDate,
                        maxDate,
                        onChange,
                        setActiveSelector
                      );
                  }}>
                  <Text.span>{day}</Text.span>
                </div>
              ))}
              {activeNextMonthDays.map((day) => (
                <div
                  key={uniqid('sds_DatePicker_Calendar_daysOfMonth_Day_')}
                  className={classNames(
                    'sds_DatePicker_Calendar_daysOfNextMonth_Day',
                    {
                      sds_DatePicker_Calendar_daysOfMonth_Day_CurrentDay:
                        checkCurrentDay(
                          day,
                          'nextMonth',
                          activeDateObj,
                          currentDateObj
                        ),
                      sds_DatePicker_Calendar_daysOfMonth_Day_SelectedDay:
                        checkSelectedDate(
                          day,
                          'nextMonth',
                          activeDateObj,
                          selectedDates.startDate
                        ) ||
                        checkSelectedDate(
                          day,
                          'nextMonth',
                          activeDateObj,
                          selectedDates.endDate
                        ),
                      sds_DatePicker_Calendar_daysOfMonth_Day_Range:
                        checkDateRange(
                          'nextMonth',
                          activeDateObj,
                          selectedDates.startDate,
                          selectedDates.endDate,
                          day
                        ),
                      sds_DatePicker_Calendar_daysOfMonth_Day_HoveredRange:
                        checkHoverDateRange(
                          'nextMonth',
                          activeDateObj,
                          selectedDates.startDate,
                          selectedDates.endDate,
                          hoverDate,
                          day
                        ),
                      sds_DatePicker_Calendar_daysOfMonth_Day_Disabled:
                        checkMinMaxDayConditionals(
                          day,
                          'nextMonth',
                          activeDateObj,
                          minDate,
                          maxDate
                        ) ||
                        checkRangeLimit(
                          day,
                          'nextMonth',
                          activeDateObj,
                          rangeDateLimit
                        ),
                    }
                  )}
                  onMouseOver={() => {
                    rangeSelectDay(
                      'hover',
                      'nextMonth',
                      day,
                      selectedDates,
                      setSelectedDates,
                      setHoverDate,
                      activeDateObj,
                      minDate,
                      maxDate,
                      onChange,
                      setActiveSelector
                    );
                  }}
                  onClick={() => {
                    rangeLimit
                      ? !checkRangeLimit(
                        day,
                        'nextMonth',
                        activeDateObj,
                        rangeDateLimit
                      ) &&
                        rangeSelectDay(
                          'select',
                          'nextMonth',
                          day,
                          selectedDates,
                          setSelectedDates,
                          setHoverDate,
                          activeDateObj,
                          minDate,
                          maxDate,
                          onChange,
                          setActiveSelector
                        )
                      : rangeSelectDay(
                        'select',
                        'nextMonth',
                        day,
                        selectedDates,
                        setSelectedDates,
                        setHoverDate,
                        activeDateObj,
                        minDate,
                        maxDate,
                        onChange,
                        setActiveSelector
                      );
                  }}>
                  <Text.span>{day}</Text.span>
                </div>
              ))}
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default React.forwardRef(Calendar);
