import React from 'react';

import classNames from 'classnames';

import type { Props } from './types';
import './radio-raw.scss';

const RadioRaw = (
  {
    className,
    inputClassName,
    value,
    id,
    name,
    defaultChecked = false,
    error,
    disabled,
    onChange,
  }: Props,
  ref: any
) => {
  const onChangeEvent = (e: any) => {
    onChange &&
      onChange({
        id,
        name,
        value,
        checked: e.target.checked,
      });
  };

  return (
    <>
      <input
        type="radio"
        className={classNames('sds_RadioRaw_Input', {
          [String(inputClassName)]: inputClassName,
        })}
        id={id}
        value={value}
        name={name}
        defaultChecked={defaultChecked}
        onChange={onChangeEvent}
        ref={ref}
      />
      <label
        className={classNames('sds_RadioRaw', {
          sds_RadioRaw_Error: error,
          sds_RadioRaw_Disabled: disabled,
          [String(className)]: className,
        })}
        htmlFor={disabled ? '' : id}
      />
    </>
  );
};

export default React.forwardRef(RadioRaw);
