import React, { useState, useEffect, useRef } from 'react';

import classNames from 'classnames';

import iro from '@jaames/iro';

import { Button } from '../../GeneralComponents/Button';
import { NumberInput } from '../NumberInput';
import { TextInput } from '../TextInput';
import { getInitialValue } from './helper';
import type { PropsTool } from './types';
import './color_picker_tool.scss';

const ColorPickerTool = (
  { color, alpha, show, onChange, className }: PropsTool,
  ref: any
) => {
  // DOM Elements
  const refC = useRef<any>(null);
  const colorPicker = useRef();
  // Color Inputs
  const refHex = useRef<any>(null);

  const refR = useRef<any>(null);
  const refG = useRef<any>(null);
  const refB = useRef<any>(null);
  const refA = useRef<any>(null);

  const refH = useRef<any>(null);
  const refS = useRef<any>(null);
  const refL = useRef<any>(null);
  const refAhsl = useRef<any>(null);

  // States
  const [codeType, setCodeType] = useState('rgba');
  const [selectedColor, setSelectedColor] = useState(
    getInitialValue(color)
    //   {
    //   hex: color,
    //   rgba: { r: '', g: '', b: '', a: '' },
    //   hsla: { h: '', s: '', l: '', a: '' },
    // }
  );

  // Functions
  const setColor = (hex: string, rgba: any, hsla: any) => {
    setSelectedColor({ hex, rgba, hsla });
    if (onChange) {
      onChange({ hex, rgba, hsla });
    }
  };

  const changeCodeType = () => {
    if (codeType === 'rgba') {
      setCodeType('hsla');
    } else {
      setCodeType('rgba');
    }
  };

  const changeHex = (e: any) => {
    setSelectedColor({
      ...selectedColor,
      hex: e.value,
    });
    if (onChange) {
      onChange({
        ...selectedColor,
        hex: e.value,
      });
    }
  };

  const changeRGB = (e: any) => {
    setSelectedColor({
      ...selectedColor,
      rgba: {
        ...selectedColor.rgba,
        [e.name]: e.value,
      },
    });

    if (onChange) {
      onChange({
        ...selectedColor,
        rgba: {
          ...selectedColor.rgba,
          [e.name]: e.value,
        },
      });
    }
  };

  const changeHSL = (e: any) => {
    setSelectedColor({
      ...selectedColor,
      hsla: {
        ...selectedColor.hsla,
        [e.name]: e.value,
      },
    });

    if (onChange) {
      onChange({
        ...selectedColor,
        rgba: {
          ...selectedColor.rgba,
          [e.name]: e.value,
        },
      });
    }
  };

  const eyeDropperAction = async () => {
    // @ts-expect-error
    const eyeDropper = new EyeDropper();
    const pickedColor = await eyeDropper.open();
    await setSelectedColor({ ...selectedColor, hex: pickedColor.sRGBHex });
    refHex.current.dispatchEvent(new Event('change'));
    refHex.current.focus();
    refHex.current.select();
  };

  const validateEyeDropperPresence = () => {
    if (window.navigator.userAgent.includes('Linux')) {
      return false;
    }
    const sUsrAg = navigator.userAgent;

    if (sUsrAg.includes('Chrome')) {
      return true;
    }
    if (sUsrAg.includes('Safari')) {
      return false;
    }
    if (sUsrAg.includes('Opera')) {
      return true;
    }
    if (sUsrAg.includes('Firefox')) {
      return false;
    }
    if (sUsrAg.includes('MSIE')) {
      return false;
    }
    return true;
  };

  const focusHex = () => {
    refHex.current.select();
  };

  // Initializing Iro Color Picker
  useEffect(() => {
    // @ts-expect-error
    const cp = (colorPicker.current = new iro.ColorPicker(refC.current, {
      color,
      width: 232,
      layout: alpha
        ? [
          {
            component: iro.ui.Box,
            options: {
              sliderType: 'hue',
            },
          },
          {
            component: iro.ui.Slider,
            options: {
              sliderType: 'hue',
            },
          },
          {
            component: iro.ui.Slider,
            options: {
              sliderType: 'alpha',
            },
          },
        ]
        : [
          {
            component: iro.ui.Box,
            options: {
              sliderType: 'hue',
            },
          },
          {
            component: iro.ui.Slider,
            options: {
              sliderType: 'hue',
            },
          },
        ],
    }));

    // Component Event
    cp.on('color:change', (newColor: any) => {
      if (setColor) {
        if (alpha) {
          setColor(newColor.hex8String, newColor.rgba, newColor.hsla);
        } else {
          setColor(newColor.hexString, newColor.rgb, newColor.hsl);
        }
      }
    });

    // Initializing colors in react state
    if (cp.color.hex) {
      setSelectedColor({
        hex: cp.color.hex,
        rgba: cp.color.rgba,
        hsla: cp.color.hsla,
      });
    }

    // Input Events
    refHex.current.addEventListener('change', (e: any) => {
      cp.color.hexString = e.target.value;
    });

    refR.current.addEventListener('change', (e: any) => {
      cp.color.rgba = {
        r: e.target.value,
        g: refG.current.value,
        b: refB.current.value,
        a: alpha ? refA.current.value : '1',
      };
    });
    refG.current.addEventListener('change', (e: any) => {
      cp.color.rgba = {
        r: refR.current.value,
        g: e.target.value,
        b: refB.current.value,
        a: alpha ? refA.current.value : '1',
      };
    });
    refB.current.addEventListener('change', (e: any) => {
      cp.color.rgba = {
        r: refR.current.value,
        g: refG.current.value,
        b: e.target.value,
        a: alpha ? refA.current.value : '1',
      };
    });
    alpha &&
      refA.current.addEventListener('change', (e: any) => {
        cp.color.rgba = {
          r: refR.current.value,
          g: refG.current.value,
          b: refB.current.value,
          a: e.target.value,
        };
      });

    refH.current.addEventListener('change', (e: any) => {
      cp.color.hsla = {
        h: e.target.value,
        s: refS.current.value,
        l: refS.current.value,
        a: alpha ? refAhsl.current.value : '1',
      };
    });
    refS.current.addEventListener('change', (e: any) => {
      cp.color.hsla = {
        h: refH.current.value,
        s: e.target.value,
        l: refL.current.value,
        a: alpha ? refAhsl.current.value : '1',
      };
    });
    refL.current.addEventListener('change', (e: any) => {
      cp.color.hsla = {
        h: refH.current.value,
        s: refS.current.value,
        l: e.target.value,
        a: alpha ? refAhsl.current.value : '1',
      };
    });
    alpha &&
      refAhsl.current.addEventListener('change', (e: any) => {
        cp.color.hsla = {
          h: refH.current.value,
          s: refS.current.value,
          l: refL.current.value,
          a: e.target.value,
        };
      });

    return () => {
      if (refC.current) {
        refC.current.removeChild(refC.current.firstChild);
      }
    };
  }, [alpha]);

  // DOM
  return (
    <div
      className={classNames('sds_ColorPickerTool', {
        sds_ColorPickerTool_Alpha: alpha,
        sds_ColorPickerTool_Hide: !show,
        sds_ColorPickerTool_EyeDropper: validateEyeDropperPresence(),
        [String(className)]: className,
      })}
      ref={ref}>
      <div className="sds_ColorPickerTool_Box" ref={refC} />
      <div className="sds_ColorPickerTool_FirstLine">
        <TextInput
          label="Hex"
          value={selectedColor.hex ?? color}
          id="sds_ColorPickerTool_Hex"
          onChange={changeHex}
          onFocus={focusHex}
          ref={refHex}
        />
        {validateEyeDropperPresence() && (
          <Button icon="eye-dropper" onClick={eyeDropperAction} />
        )}
        <div className="sds_ColorPickerTool_Swatch">
          <div
            style={{
              background: selectedColor.hex ?? color,
            }}
          />
        </div>
      </div>
      <div className="sds_ColorPickerTool_SecondLine">
        <div
          className={classNames('sds_ColorPickerTool_Box_ColorCode', {
            sds_ColorPickerTool_Box_ColorCode_Hidden: codeType !== 'rgba',
            sds_ColorPickerTool_Box_ColorCode_Alpha: alpha,
          })}>
          <NumberInput
            id="sds_ColorPickerTool_R"
            label="R"
            ref={refR}
            name="r"
            hideButtons
            value={Number(selectedColor.rgba.r)}
            onChange={changeRGB}
          />
          <NumberInput
            id="sds_ColorPickerTool_G"
            label="G"
            ref={refG}
            name="g"
            hideButtons
            value={Number(selectedColor.rgba.g)}
            onChange={changeRGB}
          />
          <NumberInput
            id="sds_ColorPickerTool_R"
            label="B"
            ref={refB}
            name="b"
            hideButtons
            value={Number(selectedColor.rgba.b)}
            onChange={changeRGB}
          />
          {alpha && (
            <NumberInput
              id="sds_ColorPickerTool_A"
              label="A"
              hideButtons
              ref={refA}
              name="a"
              value={Number(selectedColor.rgba.a)}
              onChange={changeRGB}
            />
          )}
        </div>
        <div
          className={classNames('sds_ColorPickerTool_Box_ColorCode', {
            sds_ColorPickerTool_Box_ColorCode_Hidden: codeType === 'rgba',
            sds_ColorPickerTool_Box_ColorCode_Alpha: alpha,
          })}>
          <NumberInput
            id="sds_ColorPickerTool_H"
            label="H"
            ref={refH}
            name="h"
            hideButtons
            value={Number(selectedColor.hsla.h)}
            onChange={changeHSL}
          />
          <NumberInput
            id="sds_ColorPickerTool_S"
            label="S"
            ref={refS}
            name="s"
            hideButtons
            value={Number(selectedColor.hsla.s)}
            onChange={changeHSL}
          />
          <NumberInput
            id="sds_ColorPickerTool_L"
            label="L"
            ref={refL}
            name="l"
            hideButtons
            value={Number(selectedColor.hsla.l)}
            onChange={changeHSL}
          />
          {alpha && (
            <NumberInput
              id="sds_ColorPickerTool_Ahsl"
              label="A"
              ref={refAhsl}
              name="a"
              hideButtons
              value={Number(selectedColor.hsla.a)}
              onChange={changeHSL}
            />
          )}
        </div>
        <Button icon="carets-sortable" type="Ghost" onClick={changeCodeType} />
      </div>
    </div>
  );
};

export default React.forwardRef(ColorPickerTool);
