import React, { useState } from 'react';

import classNames from 'classnames';

import { Telicon } from '../../GeneralComponents/Telicon';
import type { Props, InputFocusEvent } from './types';
import './search.scss';

const parseAlphanumericInput = (value: string) =>
  value.replace(/[^0-9a-zA-Z]/g, '').replace(/(\..*)\./g, '$1');

const parseLettersOnlyInput = (value: string) =>
  value.replace(/[^a-zA-Z]/g, '').replace(/(\..*)\./g, '$1');

const formatInputValue = (
  value: string,
  maxLength?: number,
  alphanumericOnly?: boolean,
  lettersOnly?: boolean,
  filterFn?: (value: string) => string
) => {
  let formattedValue = value || '';

  if (maxLength) {
    formattedValue = formattedValue.slice(0, maxLength);
  }

  if (alphanumericOnly) {
    formattedValue = parseAlphanumericInput(formattedValue);
  } else if (lettersOnly) {
    formattedValue = parseLettersOnlyInput(formattedValue);
  }

  if (filterFn) {
    formattedValue = filterFn(formattedValue);
  }

  return formattedValue;
};

const Search = (
  {
    className,
    value: controlledValue,
    id,
    name = '',
    placeholder = '',
    disabled,
    maxLength,
    lettersOnly,
    alphanumericOnly,
    customFilter,
    onChange,
    onBlur,
    onFocus,
  }: Props,
  ref: any
) => {
  const isInteractivityDisabled = disabled;
  const [innerValue, setInnerValue] = useState('');
  const value = controlledValue || innerValue;
  const formattedValue = formatInputValue(
    value,
    maxLength,
    alphanumericOnly,
    lettersOnly,
    customFilter
  );

  const onChangeEvent = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = event.target.value;
    setInnerValue(newValue);

    if (!onChange || isInteractivityDisabled) return;

    const newFormattedValue = formatInputValue(
      newValue,
      maxLength,
      alphanumericOnly,
      lettersOnly,
      customFilter
    );

    onChange({
      name,
      id,
      value: newFormattedValue,
      event,
    });
  };

  const onBlurEvent = (event: InputFocusEvent<HTMLInputElement>) => {
    if (!onBlur || isInteractivityDisabled) return;

    onBlur({
      name,
      id,
      value: event.target.value,
      event,
    });
  };

  const onFocusEvent = (event: InputFocusEvent<HTMLInputElement>) => {
    if (!onFocus || isInteractivityDisabled) return;

    onFocus({
      name,
      id,
      value: event.target.value,
      event,
    });
  };

  const cleanValue = () => {
    if (!disabled) {
      setInnerValue('');
      if (onChange) {
        onChange({
          name,
          id,
          value: '',
          event: undefined,
        });
      }
    }
  };

  return (
    <div
      className={classNames('sds_Search', {
        sds_Search_Disabled: disabled,
        [String(className)]: className,
      })}>
      <Telicon name="search" size="Default" />
      <input
        id={id}
        name={name}
        type="text"
        value={formattedValue}
        placeholder={placeholder}
        disabled={disabled}
        onFocus={onFocusEvent}
        onBlur={onBlurEvent}
        onChange={onChangeEvent}
        ref={ref}
      />
      {innerValue.length > 0 && (
        <Telicon
          className="sds_Search_Telicon"
          name="x--circle"
          size="Default"
          onClick={cleanValue}
        />
      )}
    </div>
  );
};

export default React.forwardRef(Search);
