import { subDays, addDays } from 'date-fns';

export const singleDateRegExp =
  /^(0?[1-9]|1[0-2])\/(0?[1-9]|1\d|2\d|3[01])\/(19|20)\d{2}$/;

// GENERAL FUNCTIONS
const getWeekDaysArray = (date: Date, lang: string) => {
  const weekDays = [];
  const weekDaysLong = [];

  for (let i = 0; i <= 6; i++) {
    const first = date.getDate() - date.getDay() + i;
    const day = new Date(date.setDate(first)).toLocaleDateString(lang, {
      weekday: lang === 'en' || lang === 'es' ? 'long' : 'narrow',
    });
    const dayLong = new Date(date.setDate(first)).toLocaleDateString(lang, {
      weekday: 'long',
    });
    if (lang === 'en' || lang === 'es') {
      weekDays.push(day.slice(0, 2));
    } else {
      weekDays.push(day);
    }
    weekDaysLong.push(dayLong);
  }

  return { weekDays, weekDaysLong };
};

const getMonths = (date: Date, lang: string) => {
  const months = [];
  // It is necesary to create a new date and making sure it starts with 01 on the days. This is important because if we pass a day value like 29 and we try to get febrary (month that only has 28 days) it will return march instead february
  const currentYear = date.getFullYear();
  const newDate = new Date(`01/01/${currentYear}`);

  for (let i = 0; i <= 11; i++) {
    const first = newDate.getMonth() - newDate.getMonth() + i;
    const month = new Date(newDate.setMonth(first)).toLocaleDateString(lang, {
      month: 'long',
    });
    months.push(month);
  }

  return months;
};

const getMonthDaysArray = (
  activeYear: number,
  activeNumericMonth: number,
  monthToGet: string,
  firstDayOfTheMonth: string,
  lastDayOfMonth: string,
  lang: string
) => {
  const weekDaysArray = getGeneralCalendarData(lang).longWeekDaysArray;

  if (monthToGet === 'last') {
    const firstDayOfTheMonthPosition =
      weekDaysArray.indexOf(firstDayOfTheMonth);
    if (activeNumericMonth !== 0) {
      const newArray: number[] = [];
      if (firstDayOfTheMonthPosition !== 0) {
        for (let i = firstDayOfTheMonthPosition - 1; i >= 0; i--) {
          newArray.push(
            Number(new Date(activeYear, activeNumericMonth - 1, 0).getDate()) -
              i
          );
        }
        return newArray;
      }
      return [];
    }
    const newArray: number[] = [];
    if (firstDayOfTheMonthPosition !== 0) {
      for (let i = firstDayOfTheMonthPosition - 1; i >= 0; i--) {
        newArray.push(Number(new Date(activeYear - 1, 11, 0).getDate()) - i);
      }
      return newArray;
    }
    return [];
  }

  if (monthToGet === 'next') {
    const lastDayOfTheMonthPosition =
      getGeneralCalendarData(lang).longWeekDaysArray.indexOf(lastDayOfMonth);

    const newArray: number[] = [];

    if (
      lastDayOfTheMonthPosition <
      getGeneralCalendarData(lang).longWeekDaysArray.length
    ) {
      let nextMonthDay = 0;
      for (
        let i = lastDayOfTheMonthPosition;
        i < weekDaysArray.length - 1;
        i++
      ) {
        nextMonthDay += 1;
        newArray.push(nextMonthDay);
      }

      return newArray;
    }
    return [];
  }

  const arrayOfDays: number[] = [];

  for (
    let i = 1;
    i <= Number(new Date(activeYear, activeNumericMonth, 0).getDate());
    i++
  ) {
    arrayOfDays.push(i);
  }

  return arrayOfDays;
};

const getCalendarActiveDateInfo = (dateString: string, lang = 'en') => {
  const dateObj: Date = new Date(dateString);

  const activeNumericMonth = Number(
    dateObj.toLocaleDateString(lang, { month: 'numeric' })
  );
  const activeYear: number = dateObj.getFullYear();

  const daysOfActiveMonth = Number(
    new Date(activeYear, activeNumericMonth, 0).getDate()
  );
  const firstDayOfTheMonth = new Date(
    `${activeNumericMonth}/01/${activeYear}`
  ).toLocaleDateString(lang, { weekday: 'long' });

  const lastDayOfMonth = new Date(
    `${activeNumericMonth}/${daysOfActiveMonth}/${activeYear}`
  ).toLocaleDateString(lang, { weekday: 'long' });

  const activeMonthDaysArray: number[] = getMonthDaysArray(
    activeYear,
    activeNumericMonth,
    'current',
    firstDayOfTheMonth,
    lastDayOfMonth,
    lang
  );
  const activeLastMontDaysArray: number[] = getMonthDaysArray(
    activeYear,
    activeNumericMonth,
    'last',
    firstDayOfTheMonth,
    lastDayOfMonth,
    lang
  );
  const activeNextMonthDaysArray: number[] = getMonthDaysArray(
    activeYear,
    activeNumericMonth,
    'next',
    firstDayOfTheMonth,
    lastDayOfMonth,
    lang
  );

  return {
    activeDateObj: dateObj,
    activeMonthDaysArray,
    activeLastMontDaysArray,
    activeNextMonthDaysArray,
  };
};

export const formatStringDate = (month: number, day: number, year: number) => {
  const stringifyMonth = () =>
    String(month).length === 1 ? `0${month}` : String(month);

  const stringifyDay = () =>
    String(day).length === 1 ? `0${day}` : String(day);

  return `${stringifyMonth()}/${stringifyDay()}/${String(year)}`;
};

export const getGeneralCalendarData = (lang = 'en') => {
  const dateObj = new Date();
  const shortWeekDaysArray = getWeekDaysArray(dateObj, lang).weekDays;
  const longWeekDaysArray = getWeekDaysArray(dateObj, lang).weekDaysLong;
  const monthsArray = getMonths(dateObj, lang);

  return { shortWeekDaysArray, longWeekDaysArray, monthsArray };
};

export const getCurrentDateInfo = (lang = 'en') => {
  const currentDateObj = new Date();
  const currentDate = currentDateObj.toLocaleDateString(lang, {
    day: '2-digit',
  });
  const currentMonth = currentDateObj.toLocaleDateString(lang, {
    month: '2-digit',
  });
  const currentYear = currentDateObj.getFullYear();
  const currentDateString = `${currentMonth}/${currentDate}/${currentYear}`;

  return { currentDateObj, currentDateString };
};

export const getInitialYearList = (year: number) => {
  const yearList: number[] = [];

  for (let i = year - 10; i <= year; i++) {
    yearList.push(i);
  }

  for (let i = year + 1; i <= year + 13; i++) {
    yearList.push(i);
  }

  return yearList;
};

// GENERAL VALIDATIONS
const checkMonthValue = (
  monthPosition: 'lastMonth' | 'currentMonth' | 'nextMonth',
  month: number
) => {
  if (monthPosition === 'lastMonth') {
    return month === 1 ? 12 : month - 1;
  }

  if (monthPosition === 'nextMonth') {
    return month === 12 ? 1 : month + 1;
  }

  return month;
};

const checkYearValue = (
  monthPosition: 'lastMonth' | 'currentMonth' | 'nextMonth',
  month: number,
  year: number
) => {
  if (monthPosition === 'lastMonth') {
    return month === 1 ? year - 1 : year;
  }

  if (monthPosition === 'nextMonth') {
    return month === 12 ? year + 1 : year;
  }

  return year;
};

const checkDayConditional = (
  month: number,
  year: number,
  day: number,
  date: string,
  monthPosition: 'lastMonth' | 'currentMonth' | 'nextMonth',
  conditionalType: 'Min' | 'Max'
) => {
  const checkedMonth = checkMonthValue(monthPosition, month);
  const checkedYear = checkYearValue(monthPosition, month, year);

  if (conditionalType === 'Max') {
    const maxDateTime = new Date(date).getTime();
    const currenDayTime = new Date(
      `${checkedMonth}/${day}/${checkedYear}`
    ).getTime();

    return !(currenDayTime <= maxDateTime);
  }

  const minDateTime = new Date(date).getTime();
  const currenDayTime = new Date(
    `${checkedMonth}/${day}/${checkedYear}`
  ).getTime();

  return !(currenDayTime >= minDateTime);
};

export const getCalendarState = (dateString: string, lang: string) => {
  const {
    activeDateObj,
    activeMonthDaysArray,
    activeLastMontDaysArray,
    activeNextMonthDaysArray,
  } = getCalendarActiveDateInfo(dateString, lang);

  return {
    activeDateObj,
    activeMonthDays: activeMonthDaysArray,
    activeLastMontDays: activeLastMontDaysArray,
    activeNextMonthDays: activeNextMonthDaysArray,
  };
};

export const checkCurrentDay = (
  day: number,
  monthPosition: 'lastMonth' | 'currentMonth' | 'nextMonth',
  activeDateObj: Date,
  currentDateObj: Date
) => {
  const activeMonth = activeDateObj.getMonth() + 1;
  const activeYear = activeDateObj.getFullYear();

  const monthPositionOptions = {
    lastMonth: () => {
      const checkedMonth = activeMonth === 1 ? 12 : activeMonth - 1;
      const checkedYear = activeMonth === 1 ? activeYear - 1 : activeYear;
      return (
        checkedMonth === currentDateObj.getMonth() + 1 &&
        day === currentDateObj.getDate() &&
        checkedYear === currentDateObj.getFullYear()
      );
    },

    currentMonth: () =>
      activeMonth === currentDateObj.getMonth() + 1 &&
      day === currentDateObj.getDate() &&
      activeYear === currentDateObj.getFullYear(),

    nextMonth: () => {
      const checkedMonth = activeMonth === 12 ? 1 : activeMonth + 1;
      const checkedYear = activeMonth === 12 ? activeYear + 1 : activeYear;
      return (
        checkedMonth === currentDateObj.getMonth() + 1 &&
        day === currentDateObj.getDate() &&
        checkedYear === currentDateObj.getFullYear()
      );
    },
  };

  return monthPositionOptions[monthPosition]();
};

export const checkSelectedDate = (
  day: number,
  monthPosition: 'lastMonth' | 'currentMonth' | 'nextMonth',
  activeDateObj: Date,
  selectedDate: string
) => {
  const selectedDateObj = new Date(selectedDate);
  const activeMonth = activeDateObj.getMonth() + 1;
  const activeYear = activeDateObj.getFullYear();

  const monthPositionOptions = {
    lastMonth: () => {
      const checkedMonth = activeMonth === 1 ? 12 : activeMonth - 1;
      const checkedYear = activeMonth === 1 ? activeYear - 1 : activeYear;

      return (
        checkedMonth === selectedDateObj.getMonth() + 1 &&
        day === selectedDateObj.getDate() &&
        checkedYear === selectedDateObj.getFullYear()
      );
    },
    currentMonth: () =>
      activeMonth === selectedDateObj.getMonth() + 1 &&
      day === selectedDateObj.getDate() &&
      activeYear === selectedDateObj.getFullYear(),
    nextMonth: () => {
      const checkedMonth = activeMonth === 12 ? 1 : activeMonth + 1;
      const checkedYear = activeMonth === 12 ? activeYear + 1 : activeYear;

      return (
        checkedMonth === selectedDateObj.getMonth() + 1 &&
        day === selectedDateObj.getDate() &&
        checkedYear === selectedDateObj.getFullYear()
      );
    },
  };

  return monthPositionOptions[monthPosition]();
};

export const checkMinMaxDayConditionals = (
  day: number,
  monthPosition: 'lastMonth' | 'currentMonth' | 'nextMonth',
  activeDateObj: Date,
  minDate?: string,
  maxDate?: string
) => {
  const activeMonth = activeDateObj.getMonth() + 1;
  const activeYear = activeDateObj.getFullYear();
  let minDateValidation = false;
  let maxDateValidation = false;

  const monthPositionOptions = {
    lastMonth: () => {
      if (minDate || maxDate) {
        if (minDate) {
          minDateValidation = checkDayConditional(
            activeMonth,
            activeYear,
            day,
            minDate,
            'lastMonth',
            'Min'
          );
        }
        if (maxDate) {
          maxDateValidation = checkDayConditional(
            activeMonth,
            activeYear,
            day,
            maxDate,
            'lastMonth',
            'Max'
          );
        }
        return minDateValidation || maxDateValidation;
      }
      return false;
    },
    currentMonth: () => {
      if (minDate || maxDate) {
        if (minDate) {
          minDateValidation = checkDayConditional(
            activeMonth,
            activeYear,
            day,
            minDate,
            'currentMonth',
            'Min'
          );
        }
        if (maxDate) {
          maxDateValidation = checkDayConditional(
            activeMonth,
            activeYear,
            day,
            maxDate,
            'currentMonth',
            'Max'
          );
        }
        return minDateValidation || maxDateValidation;
      }
      return false;
    },
    nextMonth: () => {
      if (minDate || maxDate) {
        if (minDate) {
          minDateValidation = checkDayConditional(
            activeMonth,
            activeYear,
            day,
            minDate,
            'nextMonth',
            'Min'
          );
        }
        if (maxDate) {
          maxDateValidation = checkDayConditional(
            activeMonth,
            activeYear,
            day,
            maxDate,
            'nextMonth',
            'Max'
          );
        }
        return minDateValidation || maxDateValidation;
      }
      return false;
    },
  };

  return monthPositionOptions[monthPosition]();
};

export const checkBtnConditional = (
  condition: 'Min' | 'Max',
  date: string,
  activeDateObj: Date
) => {
  const activeMonth = activeDateObj.getMonth() + 1;
  const activeYear = activeDateObj.getFullYear();

  const activeDateTime = new Date(`${activeMonth}/01/${activeYear}`).getTime();
  const dateObj: any = new Date(date);
  const dateTime =
    condition === 'Min'
      ? new Date(
        `${dateObj.getMonth() + 1}/01/${dateObj.getFullYear()}`
      ).getTime()
      : new Date(
        `${dateObj.getMonth() + 1}/01/${dateObj.getFullYear()}`
      ).getTime();

  if (condition === 'Min') {
    return !(activeDateTime > dateTime);
  }

  return !(activeDateTime < dateTime);
};

export const checkDateRange = (
  monthPosition: 'lastMonth' | 'currentMonth' | 'nextMonth',
  activeDateObj: Date,
  startDate: string,
  endDate: string,
  day: number
) => {
  const activeMonth = activeDateObj.getMonth() + 1;
  const activeYear = activeDateObj.getFullYear();

  const month = checkMonthValue(monthPosition, activeMonth);
  const year = checkYearValue(monthPosition, activeMonth, activeYear);

  const startDateTime = new Date(startDate).getTime();
  const endDateTime = new Date(endDate).getTime();
  const currentDayTime = new Date(`${month}/${day}/${year}`).getTime();

  if (startDate && endDate) {
    return !!(currentDayTime > startDateTime && currentDayTime < endDateTime);
  }
  return false;
};

export const checkYearConditional = (
  year: number,
  minDate: any,
  maxDate: any
) => {
  let minDateValidation = false;
  let maxDateValidation = false;

  if (minDate) {
    const minDateYear = new Date(minDate).getFullYear();

    if (year < minDateYear) {
      minDateValidation = true;
    } else {
      minDateValidation = false;
    }
  }

  if (maxDate) {
    const maxDateYear = new Date(maxDate).getFullYear();

    if (year > maxDateYear) {
      maxDateValidation = true;
    } else {
      maxDateValidation = false;
    }
  }

  return minDateValidation || maxDateValidation;
};

export const checkYearBtnConditional = (
  yearList: any[],
  maxDate: string | undefined,
  minDate: string | undefined
) => {
  let maxDateValidation = false;
  let minDateValidation = false;

  if (maxDate) {
    const maxDateYear = new Date(maxDate).getFullYear();

    if (yearList[yearList.length - 1] + 1 <= maxDateYear) {
      maxDateValidation = false;
    } else {
      maxDateValidation = true;
    }
  }

  if (minDate) {
    const minDateYear = new Date(minDate).getFullYear();

    if (yearList[0] - 1 >= minDateYear) {
      minDateValidation = false;
    } else {
      minDateValidation = true;
    }
  }

  return { maxDateValidation, minDateValidation };
};

// CALENDAR NAVIGATION
export const prevMonth = (
  activeDateObj: Date,
  setActiveDate: any,
  lang: string
) => {
  let newMonth = '';
  let newYear = '';
  const activeMonth = activeDateObj.getMonth() + 1;
  const activeYear = activeDateObj.getFullYear();

  if (activeMonth !== 1) {
    newYear = String(activeYear);
    if (activeMonth <= 10) {
      newMonth = `0${activeMonth - 1}`;
    } else {
      newMonth = String(activeMonth - 1);
    }
  } else {
    newYear = String(activeYear - 1);
    newMonth = '12';
  }

  setActiveDate(getCalendarState(`${newMonth}/01/${newYear}`, lang));
};

export const nextMonth = (
  activeDateObj: Date,
  setActiveDate: any,
  lang: string
) => {
  let newMonth = '';
  let newYear = '';
  const activeMonth = activeDateObj.getMonth() + 1;
  const activeYear = activeDateObj.getFullYear();

  if (activeMonth !== 12) {
    newYear = String(activeYear);
    if (activeMonth < 9) {
      newMonth = `0${activeMonth + 1}`;
    } else {
      newMonth = String(activeMonth + 1);
    }
  } else {
    newYear = String(activeYear + 1);
    newMonth = '01';
  }

  setActiveDate(getCalendarState(`${newMonth}/01/${newYear}`, lang));
};

export const nextYearList = (lastYearOfTheList: number, setYearList: any) => {
  const yearList: number[] = [];

  for (let i = lastYearOfTheList + 1; i <= lastYearOfTheList + 24; i++) {
    yearList.push(i);
  }

  setYearList(yearList);
};

export const previousYearList = (
  firstYearOfTheList: number,
  setYearList: any
) => {
  const yearList: number[] = [];

  for (let i = firstYearOfTheList - 24; i <= firstYearOfTheList - 1; i++) {
    yearList.push(i);
  }

  setYearList(yearList);
};

export const updateYear = (
  selectedYear: number,
  yearValidation: boolean,
  activeDateObj: Date,
  setActiveDate: any,
  setYearMenu: any,
  lang: string
) => {
  if (!yearValidation) {
    setActiveDate(
      getCalendarState(
        formatStringDate(
          activeDateObj.getMonth() + 1,
          activeDateObj.getDate(),
          selectedYear
        ),
        lang
      )
    );
    setYearMenu(false);
  }
};

// SINGLE DATE PICKER FUNCTIONS
export const selectDay = (
  monthPosition: 'lastMonth' | 'currentMonth' | 'nextMonth',
  day: number,
  activeDateObj: Date,
  minDate: string | undefined,
  maxDate: string | undefined,
  setSelectedDate: any,
  onChange: any
) => {
  const activeMonth = activeDateObj.getMonth() + 1;
  const activeYear = activeDateObj.getFullYear();

  const monthOptions = {
    lastMonth: () => {
      if (
        !checkMinMaxDayConditionals(
          day,
          'lastMonth',
          activeDateObj,
          minDate,
          maxDate
        )
      ) {
        const stringDate = formatStringDate(
          activeMonth !== 1 ? activeMonth - 1 : 12,
          day,
          activeMonth !== 1 ? activeYear : activeYear - 1
        );

        setSelectedDate(stringDate);

        onChange && onChange(stringDate);
      }
    },

    currentMonth: () => {
      if (
        !checkMinMaxDayConditionals(
          day,
          'currentMonth',
          activeDateObj,
          minDate,
          maxDate
        )
      ) {
        const stringDate = formatStringDate(activeMonth, day, activeYear);
        setSelectedDate(stringDate);
        onChange && onChange(stringDate);
      }
    },

    nextMonth: () => {
      if (
        !checkMinMaxDayConditionals(
          day,
          'nextMonth',
          activeDateObj,
          minDate,
          maxDate
        )
      ) {
        const stringDate = formatStringDate(
          activeMonth !== 12 ? activeMonth + 1 : 1,
          day,
          activeMonth !== 12 ? activeYear : activeYear + 1
        );
        setSelectedDate(stringDate);
        onChange && onChange(stringDate);
      }
    },
  };

  monthOptions[monthPosition]();
};

// RANGE DATE PICKER FUNCTIONS
export const rangeSelectDay = (
  action: 'select' | 'hover' = 'select',
  monthPosition: 'lastMonth' | 'currentMonth' | 'nextMonth',
  day: number,
  selectedDates: {
    startDate: string;
    endDate: string;
  },
  setSelectedDates: any,
  setHoverDate: any,
  activeDateObj: Date,
  minDate: any,
  maxDate: any,
  onChange: any,
  setActiveSelector: any
) => {
  const activeMonth = activeDateObj.getMonth() + 1;
  const activeYear = activeDateObj.getFullYear();

  const monthOptions = {
    lastMonth: () => {
      if (
        !checkMinMaxDayConditionals(
          day,
          'lastMonth',
          activeDateObj,
          minDate,
          maxDate
        )
      ) {
        const stringDate = formatStringDate(
          activeMonth !== 1 ? activeMonth - 1 : 12,
          day,
          activeMonth !== 1 ? activeYear : activeYear - 1
        );

        if (action === 'select') {
          if (selectedDates.startDate === '') {
            setSelectedDates({ startDate: stringDate, endDate: '' });
            onChange &&
              onChange({ startDate: stringDate, endDate: '', selector: '' });
          } else if (
            selectedDates.startDate !== '' &&
            selectedDates.endDate !== ''
          ) {
            setSelectedDates({ startDate: stringDate, endDate: '' });
            onChange &&
              onChange({ startDate: stringDate, endDate: '', selector: '' });
          }

          if (
            selectedDates.startDate !== '' &&
            selectedDates.endDate === '' &&
            new Date(stringDate) >= new Date(selectedDates.startDate)
          ) {
            setSelectedDates({ ...selectedDates, endDate: stringDate });
            onChange &&
              onChange({ ...selectedDates, endDate: stringDate, selector: '' });
          } else {
            setSelectedDates({ startDate: stringDate, endDate: '' });
            onChange &&
              onChange({ startDate: stringDate, endDate: '', selector: '' });
          }

          setActiveSelector('');
        } else {
          selectedDates.startDate !== '' &&
            selectedDates.endDate === '' &&
            setHoverDate(stringDate);
        }
      }
    },

    currentMonth: () => {
      if (
        !checkMinMaxDayConditionals(
          day,
          'currentMonth',
          activeDateObj,
          minDate,
          maxDate
        )
      ) {
        const stringDate = formatStringDate(activeMonth, day, activeYear);

        if (action === 'select') {
          if (selectedDates.startDate === '') {
            setSelectedDates({ startDate: stringDate, endDate: '' });
            onChange &&
              onChange({ startDate: stringDate, endDate: '', selector: '' });
          } else if (
            selectedDates.startDate !== '' &&
            selectedDates.endDate !== ''
          ) {
            setSelectedDates({ startDate: stringDate, endDate: '' });
            onChange &&
              onChange({ startDate: stringDate, endDate: '', selector: '' });
          }

          if (
            selectedDates.startDate !== '' &&
            selectedDates.endDate === '' &&
            new Date(stringDate) >= new Date(selectedDates.startDate)
          ) {
            setSelectedDates({ ...selectedDates, endDate: stringDate });
            onChange && onChange({ ...selectedDates, endDate: stringDate });
          } else {
            setSelectedDates({ startDate: stringDate, endDate: '' });
            onChange &&
              onChange({ startDate: stringDate, endDate: '', selector: '' });
          }

          setActiveSelector('');
        } else {
          selectedDates.startDate !== '' &&
            selectedDates.endDate === '' &&
            setHoverDate(stringDate);
        }
      }
    },

    nextMonth: () => {
      if (
        !checkMinMaxDayConditionals(
          day,
          'nextMonth',
          activeDateObj,
          minDate,
          maxDate
        )
      ) {
        const stringDate = formatStringDate(
          activeMonth !== 12 ? activeMonth + 1 : 1,
          day,
          activeMonth !== 12 ? activeYear : activeYear + 1
        );

        if (action === 'select') {
          if (selectedDates.startDate === '') {
            setSelectedDates({ startDate: stringDate, endDate: '' });
            onChange &&
              onChange({ startDate: stringDate, endDate: '', selector: '' });
          } else if (
            selectedDates.startDate !== '' &&
            selectedDates.endDate !== ''
          ) {
            setSelectedDates({ startDate: stringDate, endDate: '' });
            onChange &&
              onChange({ startDate: stringDate, endDate: '', selector: '' });
          }

          if (
            selectedDates.startDate !== '' &&
            selectedDates.endDate === '' &&
            new Date(stringDate) >= new Date(selectedDates.startDate)
          ) {
            setSelectedDates({ ...selectedDates, endDate: stringDate });
            onChange &&
              onChange({ ...selectedDates, endDate: stringDate, selector: '' });
          } else {
            setSelectedDates({ startDate: stringDate, endDate: '' });
            onChange &&
              onChange({ startDate: stringDate, endDate: '', selector: '' });
          }

          setActiveSelector('');
        } else {
          selectedDates.startDate !== '' &&
            selectedDates.endDate === '' &&
            setHoverDate(stringDate);
        }
      }
    },
  };

  monthOptions[monthPosition]();
};

export const selectionMenuFunctions = (
  selection:
  | 'today'
  | 'yesterday'
  | 'last7Days'
  | 'last30Days'
  | 'thisMonth'
  | 'lastMonth',
  currentDateObj: Date,
  setSelectedDates: any,
  setActiveSelector: any,
  onChange: any,
  setActiveDate: any,
  lang: string
) => {
  const selectionFunctionsOptions = {
    today: () => {
      const daysSelection = {
        startDate: formatStringDate(
          currentDateObj.getMonth() + 1,
          currentDateObj.getDate(),
          currentDateObj.getFullYear()
        ),
        endDate: formatStringDate(
          currentDateObj.getMonth() + 1,
          currentDateObj.getDate(),
          currentDateObj.getFullYear()
        ),
      };

      setSelectedDates(daysSelection);
      setActiveSelector('today');
      onChange && onChange({ ...daysSelection, selector: 'Today' });

      setActiveDate(
        getCalendarState(
          formatStringDate(
            currentDateObj.getMonth() + 1,
            currentDateObj.getDate(),
            currentDateObj.getFullYear()
          ),
          lang
        )
      );
    },

    yesterday: () => {
      const yesterdayObj = subDays(currentDateObj, 1);
      const daysSelection = {
        startDate: formatStringDate(
          yesterdayObj.getMonth() + 1,
          yesterdayObj.getDate(),
          yesterdayObj.getFullYear()
        ),
        endDate: formatStringDate(
          yesterdayObj.getMonth() + 1,
          yesterdayObj.getDate(),
          yesterdayObj.getFullYear()
        ),
      };

      setSelectedDates(daysSelection);
      setActiveSelector('yesterday');
      onChange && onChange({ ...daysSelection, selector: 'Yesterday' });

      setActiveDate(
        getCalendarState(
          formatStringDate(
            yesterdayObj.getMonth() + 1,
            yesterdayObj.getDate(),
            yesterdayObj.getFullYear()
          ),
          lang
        )
      );
    },

    last7Days: () => {
      const firstDay = subDays(currentDateObj, 6);
      const daysSelection = {
        startDate: formatStringDate(
          firstDay.getMonth() + 1,
          firstDay.getDate(),
          firstDay.getFullYear()
        ),
        endDate: formatStringDate(
          currentDateObj.getMonth() + 1,
          currentDateObj.getDate(),
          currentDateObj.getFullYear()
        ),
      };

      setSelectedDates(daysSelection);
      setActiveSelector('last7Days');
      onChange && onChange({ ...daysSelection, selector: 'Last 7 Days' });

      setActiveDate(
        getCalendarState(
          formatStringDate(
            currentDateObj.getMonth() + 1,
            currentDateObj.getDate(),
            currentDateObj.getFullYear()
          ),
          lang
        )
      );
    },

    last30Days: () => {
      const firstDayObj = subDays(currentDateObj, 29);
      const daysSelection = {
        startDate: formatStringDate(
          firstDayObj.getMonth() + 1,
          firstDayObj.getDate(),
          firstDayObj.getFullYear()
        ),
        endDate: formatStringDate(
          currentDateObj.getMonth() + 1,
          currentDateObj.getDate(),
          currentDateObj.getFullYear()
        ),
      };

      setSelectedDates(daysSelection);
      setActiveSelector('last30Days');
      onChange && onChange({ ...daysSelection, selector: 'Last 30 Days' });

      setActiveDate(
        getCalendarState(
          formatStringDate(
            currentDateObj.getMonth() + 1,
            currentDateObj.getDate(),
            currentDateObj.getFullYear()
          ),
          lang
        )
      );
    },

    thisMonth: () => {
      const lastDayOfMonthObj = new Date(
        currentDateObj.getFullYear(),
        currentDateObj.getMonth() + 1,
        0
      );
      const daysSelection = {
        startDate: `${
          currentDateObj.getMonth() + 1
        }/01/${currentDateObj.getFullYear()}`,
        endDate: formatStringDate(
          currentDateObj.getMonth() + 1,
          lastDayOfMonthObj.getDate(),
          currentDateObj.getFullYear()
        ),
      };

      setSelectedDates(daysSelection);
      setActiveSelector('thisMonth');
      onChange && onChange({ ...daysSelection, selector: 'This Month' });

      setActiveDate(
        getCalendarState(
          formatStringDate(
            currentDateObj.getMonth() + 1,
            lastDayOfMonthObj.getDate(),
            currentDateObj.getFullYear()
          ),
          lang
        )
      );
    },

    lastMonth: () => {
      const checkedMonth = checkMonthValue(
        'lastMonth',
        currentDateObj.getMonth() + 1
      );
      const checkedYear = checkYearValue(
        'lastMonth',
        currentDateObj.getMonth() + 1,
        currentDateObj.getFullYear()
      );

      const lastDayOfMonthObj = new Date(checkedYear, checkedMonth, 0);

      const daysSelection = {
        startDate: `${checkedMonth}/01/${checkedYear}`,
        endDate: formatStringDate(
          lastDayOfMonthObj.getMonth() + 1,
          lastDayOfMonthObj.getDate(),
          lastDayOfMonthObj.getFullYear()
        ),
      };

      setSelectedDates(daysSelection);
      setActiveSelector('lastMonth');
      onChange && onChange({ ...daysSelection, selector: 'Last Month' });

      setActiveDate(
        getCalendarState(
          formatStringDate(
            checkedMonth,
            lastDayOfMonthObj.getDate(),
            checkedYear
          ),
          lang
        )
      );
    },
  };

  return selectionFunctionsOptions[selection]();
};

export const checkHoverDateRange = (
  monthPosition: 'lastMonth' | 'currentMonth' | 'nextMonth',
  activeDateObj: Date,
  startDate: string,
  endDate: string,
  hoveredDate: string,
  day: number
) => {
  const activeMonth = activeDateObj.getMonth() + 1;
  const activeYear = activeDateObj.getFullYear();

  const month = checkMonthValue(monthPosition, activeMonth);
  const year = checkYearValue(monthPosition, activeMonth, activeYear);

  const startDateTime = new Date(startDate).getTime();
  const hoveredDateTime = new Date(hoveredDate).getTime();
  const currentDayTime = new Date(`${month}/${day}/${year}`).getTime();

  if (startDate !== '' && hoveredDate !== '' && endDate === '') {
    return !!(
      currentDayTime > startDateTime && currentDayTime <= hoveredDateTime
    );
  }
  return false;
};

export const getRangeDateLimit = (startDate: string, rangeLimit: number) => {
  const dateRangeLimitObj = addDays(new Date(startDate), rangeLimit);
  const stringifyDateRangeLimit = formatStringDate(
    dateRangeLimitObj.getMonth(),
    dateRangeLimitObj.getDate(),
    dateRangeLimitObj.getFullYear()
  );

  return stringifyDateRangeLimit;
};

export const checkRangeLimit = (
  day: number,
  monthPosition: 'lastMonth' | 'currentMonth' | 'nextMonth',
  activeDateObj: Date,
  rangeDate: string
) => {
  if (rangeDate) {
    const activeMonth = activeDateObj.getMonth() + 1;
    const activeYear = activeDateObj.getFullYear();

    const checkedMonth = checkMonthValue(monthPosition, activeMonth - 1);
    const checkedYear = checkYearValue(monthPosition, activeMonth, activeYear);

    const rangeDateTime = new Date(rangeDate).getTime();
    const dateTime = new Date(
      `${checkedMonth}/${day}/${checkedYear}`
    ).getTime();

    return !(dateTime <= rangeDateTime);
  }

  return false;
};

export const formatDate = (
  date: string,
  format: 'dd/mm/yyyy' | 'mm/dd/yyyy' | 'yyyy/mm/dd'
) => {
  const newDateObj = new Date(date);
  const month =
    String(newDateObj.getMonth() + 1).length === 1
      ? `0${newDateObj.getMonth() + 1}`
      : `${newDateObj.getMonth() + 1}`;
  const day =
    String(newDateObj.getDate()).length === 1
      ? `0${newDateObj.getDate()}`
      : `${newDateObj.getDate()}`;
  const year = newDateObj.getFullYear();

  const formats = {
    'dd/mm/yyyy': () => `${day}/${month}/${year}`,
    'mm/dd/yyyy': () => `${month}/${day}/${year}`,
    'yyyy/mm/dd': () => `${year}/${month}/${day}`,
  };

  return formats[format]();
};
