import { colorcolor } from 'colorcolor';

const getColorObjects = (rgbaString: string, hslaString: string) => {
  const rgbaFormated = rgbaString.replace('rgba(', '').replace(')', '');
  const hslaFormated = hslaString.replace('hsla(', '').replace(')', '');

  const rgbaArray = rgbaFormated.split(',');
  const hslaArray = hslaFormated.split(',');

  const rgba = { r: '', g: '', b: '', a: '' };
  const hsla = { h: '', s: '', l: '', a: '' };

  rgbaArray.forEach((value, index) => {
    if (index === 0) rgba.r = value;
    if (index === 1) rgba.g = value;
    if (index === 2) rgba.b = value;
    if (index === 3) rgba.a = value;
  });

  hslaArray.forEach((value, index) => {
    if (index === 0) hsla.h = value;
    if (index === 1) hsla.s = value;
    if (index === 2) hsla.l = value;
    if (index === 3) hsla.a = value;
  });

  return {
    rgba,
    hsla,
  };
};

export const getInitialValue = (color: string) => {
  const hex = color;
  const { rgba, hsla } = getColorObjects(
    colorcolor(hex, 'rgba'),
    colorcolor(hex, 'hsla')
  );

  return {
    hex,
    rgba,
    hsla,
  };
};
