import React from 'react';

import classNames from 'classnames';
import { ToastContainer } from 'react-toastify';
import './ReactToastify.scss';
import './toast.scss';

interface Props {
  className?: string;
  commland?: boolean;
}

const Toast = ({ className, commland }: Props) => (
  <ToastContainer
    autoClose={5000}
    hideProgressBar={false}
    newestOnTop={false}
    closeOnClick
    rtl={false}
    pauseOnFocusLoss
    draggable
    pauseOnHover
    className={classNames('sds_Toast', {
      sds_Toast_Commland: commland,
      [String(className)]: className,
    })}
  />
);

export default Toast;
