import React, { useState } from 'react';

import classNames from 'classnames';
import uniqid from 'uniqid';

import { Button } from '../../../GeneralComponents/Button';
import { Telicon } from '../../../GeneralComponents/Telicon';
import { Text } from '../../../GeneralComponents/Text';
import { useUpdate } from '../../../hooks';
import {
  getCurrentDateInfo,
  getGeneralCalendarData,
  checkCurrentDay,
  checkSelectedDate,
  checkMinMaxDayConditionals,
  checkBtnConditional,
  getInitialYearList,
  checkYearConditional,
  checkYearBtnConditional,
  singleDateRegExp,
  prevMonth,
  nextMonth,
  previousYearList,
  nextYearList,
  updateYear,
  selectDay,
  getCalendarState,
} from '../helper';
import type { Props } from './calendarTypes';
import '../calendar.scss';

const Calendar = (
  {
    lang = 'en',
    className,
    show = false,
    onChange,
    selectedDate: defaultSelectedDate = '',
    maxDate,
    minDate,
  }: Props,
  ref: any
) => {
  // States
  const [yearList, setYearList] = useState<number[]>([]);
  const [yearMenu, setYearMenu] = useState(false);
  const { currentDateObj, currentDateString } = getCurrentDateInfo(lang);
  const { shortWeekDaysArray: weekDays, monthsArray: months } =
    getGeneralCalendarData(lang);
  const [selectedDate, setSelectedDate] = useState(defaultSelectedDate);
  const [activeDate, setActiveDate] = useState(
    getCalendarState(
      defaultSelectedDate &&
        defaultSelectedDate !== '' &&
        singleDateRegExp.test(defaultSelectedDate)
        ? defaultSelectedDate
        : currentDateString,
      lang
    )
  );

  // Destructuring
  const {
    activeDateObj,
    activeMonthDays,
    activeLastMontDays,
    activeNextMonthDays,
  } = activeDate;
  const activeMonth = activeDateObj.getMonth() + 1;
  const activeYear = activeDateObj.getFullYear();

  useUpdate(() => {
    setSelectedDate(defaultSelectedDate);
  }, [defaultSelectedDate]);

  useUpdate(() => {
    if (yearMenu) {
      if (yearList.length === 0) {
        setYearList(getInitialYearList(activeYear));
      }
    }
  }, [yearMenu]);

  return (
    <div
      ref={ref}
      className={classNames('sds_DatePicker_Calendar', {
        sds_DatePicker_Calendar_Hidden: !show,
        [String(className)]: className,
      })}>
      <div className="sds_DatePicker_Calendar_Header">
        <Button
          icon="chevron-left"
          type="Ghost"
          onClick={() =>
            yearMenu
              ? previousYearList(yearList[0], setYearList)
              : prevMonth(activeDateObj, setActiveDate, lang)
          }
          disabled={
            yearMenu
              ? checkYearBtnConditional(yearList, maxDate, minDate)
                .minDateValidation
              : minDate
                ? checkBtnConditional('Min', minDate, activeDateObj)
                : false
          }
        />
        <div>
          <Text.span>{months[activeMonth - 1]}</Text.span>
          <div
            className={classNames(
              'sds_DatePicker_Calendar_Header_YearSelection',
              { sds_DatePicker_Calendar_Header_YearSelection_Active: yearMenu }
            )}
            onClick={() => {
              setYearMenu((prevState) => !prevState);
            }}>
            <Text.span>{activeYear}</Text.span>
            <Telicon name="carets-sortable" />
          </div>
        </div>
        <Button
          icon="chevron-right"
          type="Ghost"
          onClick={() =>
            yearMenu
              ? nextYearList(yearList[yearList.length - 1], setYearList)
              : nextMonth(activeDateObj, setActiveDate, lang)
          }
          disabled={
            yearMenu
              ? checkYearBtnConditional(yearList, maxDate, minDate)
                .maxDateValidation
              : maxDate
                ? checkBtnConditional('Max', maxDate, activeDateObj)
                : false
          }
        />
      </div>
      {yearMenu ? (
        <div className="sds_DatePicker_Calendar_YearList">
          {yearList.map((yearItem) => {
            const yearValidation = checkYearConditional(
              yearItem,
              minDate,
              maxDate
            );

            return (
              <div
                className={classNames('sds_DatePicker_Calendar_YearList_Year', {
                  sds_DatePicker_Calendar_YearList_Year_Current:
                    yearItem === currentDateObj.getFullYear(),
                  sds_DatePicker_Calendar_YearList_Year_Disabled:
                    yearValidation,
                })}
                key={uniqid('sds_DatePicker_Calendar_YearList_Year_')}
                onClick={() => {
                  updateYear(
                    yearItem,
                    yearValidation,
                    activeDateObj,
                    setActiveDate,
                    setYearMenu,
                    lang
                  );
                }}>
                <Text.span className="sds_DatePicker_Calendar_YearList_Year_Text">
                  {yearItem}
                </Text.span>
              </div>
            );
          })}
        </div>
      ) : (
        <>
          <div className="sds_DatePicker_Calendar_DaysOfWeek">
            {weekDays.map((day: string) => (
              <div key={uniqid('sds_DatePicker_Calendar_DaysOfWeek_Day_')}>
                <Text.span>{day}</Text.span>
              </div>
            ))}
          </div>
          <div className="sds_DatePicker_Calendar_daysOfMonth">
            {activeLastMontDays.map((day) => (
              <div
                key={uniqid('sds_DatePicker_Calendar_daysOfMonth_Day_')}
                className={classNames(
                  ' sds_DatePicker_Calendar_daysOfLastMonth_Day',
                  {
                    sds_DatePicker_Calendar_daysOfMonth_Day_CurrentDay:
                      checkCurrentDay(
                        day,
                        'lastMonth',
                        activeDateObj,
                        currentDateObj
                      ),
                    sds_DatePicker_Calendar_daysOfMonth_Day_SelectedDay:
                      checkSelectedDate(
                        day,
                        'lastMonth',
                        activeDateObj,
                        selectedDate
                      ),
                    sds_DatePicker_Calendar_daysOfMonth_Day_Disabled:
                      checkMinMaxDayConditionals(
                        day,
                        'lastMonth',
                        activeDateObj,
                        minDate,
                        maxDate
                      ),
                  }
                )}
                onClick={() => {
                  selectDay(
                    'lastMonth',
                    day,
                    activeDateObj,
                    minDate,
                    maxDate,
                    setSelectedDate,
                    onChange
                  );
                }}>
                <Text.span>{day}</Text.span>
              </div>
            ))}
            {activeMonthDays.map((day) => (
              <div
                key={uniqid('sds_DatePicker_Calendar_daysOfMonth_Day_')}
                className={classNames(
                  'sds_DatePicker_Calendar_daysOfMonth_Day',
                  {
                    sds_DatePicker_Calendar_daysOfMonth_Day_CurrentDay:
                      checkCurrentDay(
                        day,
                        'currentMonth',
                        activeDateObj,
                        currentDateObj
                      ),
                    sds_DatePicker_Calendar_daysOfMonth_Day_SelectedDay:
                      checkSelectedDate(
                        day,
                        'currentMonth',
                        activeDateObj,
                        selectedDate
                      ),
                    sds_DatePicker_Calendar_daysOfMonth_Day_Disabled:
                      checkMinMaxDayConditionals(
                        day,
                        'currentMonth',
                        activeDateObj,
                        minDate,
                        maxDate
                      ),
                  }
                )}
                onClick={() => {
                  selectDay(
                    'currentMonth',
                    day,
                    activeDateObj,
                    minDate,
                    maxDate,
                    setSelectedDate,
                    onChange
                  );
                }}>
                <Text.span>{day}</Text.span>
              </div>
            ))}
            {activeNextMonthDays.map((day) => (
              <div
                key={uniqid('sds_DatePicker_Calendar_daysOfMonth_Day_')}
                className={classNames(
                  'sds_DatePicker_Calendar_daysOfNextMonth_Day',
                  {
                    sds_DatePicker_Calendar_daysOfMonth_Day_CurrentDay:
                      checkCurrentDay(
                        day,
                        'nextMonth',
                        activeDateObj,
                        currentDateObj
                      ),
                    sds_DatePicker_Calendar_daysOfMonth_Day_SelectedDay:
                      checkSelectedDate(
                        day,
                        'nextMonth',
                        activeDateObj,
                        selectedDate
                      ),
                    sds_DatePicker_Calendar_daysOfMonth_Day_Disabled:
                      checkMinMaxDayConditionals(
                        day,
                        'nextMonth',
                        activeDateObj,
                        minDate,
                        maxDate
                      ),
                  }
                )}
                onClick={() => {
                  selectDay(
                    'nextMonth',
                    day,
                    activeDateObj,
                    minDate,
                    maxDate,
                    setSelectedDate,
                    onChange
                  );
                }}>
                <Text.span>{day}</Text.span>
              </div>
            ))}
          </div>
        </>
      )}
    </div>
  );
};

export default React.forwardRef(Calendar);
