import { toast } from 'react-toastify';

type Type = 'Default' | 'Info' | 'Success' | 'Warning' | 'Danger';
type Position =
  | 'top-left'
  | 'top-center'
  | 'top-right'
  | 'bottom-right'
  | 'bottom-center'
  | 'bottom-left';

const toastTrigger = (
  message: string,
  type: Type = 'Default',
  position: Position = 'top-right'
) => {
  switch (type) {
    case 'Info':
      return toast.info(message, {
        position,
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    case 'Success':
      return toast.success(message, {
        position,
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    case 'Warning':
      return toast.warning(message, {
        position,
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    case 'Danger':
      return toast.error(message, {
        position,
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    default:
      return toast(message, {
        position,
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
  }
};

export default toastTrigger;
