import React, { useState } from 'react';

import classNames from 'classnames';
import uniqid from 'uniqid';

import { Search } from '../../FormControls/Search';
import { useUpdate } from '../../hooks';
import { getContent } from './helper';
import type { Props } from './types';
import './menu.scss';

const Menu = (
  {
    children,
    className,
    search,
    show = false,
    onChange,
    selectedItem,
    removeSelectedItems = false,
    checkSelectedItems = false,
    noResultMessage = 'No matching result...',
    noContentMessage = 'No content available...',
  }: Props,
  ref: any
) => {
  const data = getContent(
    children,
    noContentMessage,
    selectedItem ?? '',
    checkSelectedItems,
    removeSelectedItems,
    clickEvent
  );

  const [baseContent, setBaseContent] = useState(data);
  const [content, setContent] = useState(data);
  const [innerSelection, setInnerSelection] = useState(selectedItem ?? '');
  const [filter, setFilter] = useState('');

  function clickEvent (e: any) {
    setFilter('');
    if (selectedItem === undefined) {
      setInnerSelection(e.value);
    }
    onChange && onChange(e);
  }

  const filterEvent = (e: any) => {
    const { value } = e;
    setFilter(value);

    if (value === '') {
      setContent(baseContent);
    } else {
      const newArray: any[] = baseContent.filter((element) =>
        element.props.children
          .toLowerCase()
          .replace(' ', '')
          .includes(value.toLowerCase().replace(' ', ''))
      );

      if (newArray.length > 0) {
        setContent(newArray);
      } else {
        setContent([
          <div
            className="sds_Option sds_Option_NoResult"
            key={uniqid('sds_Option_')}>
            {noResultMessage}
          </div>,
        ]);
      }
    }
  };

  useUpdate(() => {
    if (selectedItem !== undefined) {
      const newData = getContent(
        children,
        noContentMessage,
        selectedItem ?? '',
        checkSelectedItems,
        removeSelectedItems,
        clickEvent
      );

      setBaseContent(newData);
      setContent(newData);
    }
  }, [children, selectedItem]);

  useUpdate(() => {
    if (selectedItem === undefined) {
      const newData = getContent(
        children,
        noContentMessage,
        innerSelection,
        checkSelectedItems,
        removeSelectedItems,
        clickEvent
      );

      setBaseContent(newData);
      setContent(newData);
    }
  }, [children, innerSelection]);

  return (
    <div
      data-testid="sds-menu"
      className={classNames('sds_Menu', {
        sds_Menu_Hidden: !show,
        [String(className)]: className,
      })}
      ref={ref}>
      {search && (
        <div className="sds_Menu_SearchBox">
          <Search
            id="menuSearch"
            name="menuSearch"
            placeholder="Search..."
            value={filter}
            onChange={filterEvent}
          />
        </div>
      )}
      <div className="sds_Menu_Section">{content}</div>
    </div>
  );
};

export default React.forwardRef(Menu);
