import React, { useState } from 'react';

import classNames from 'classnames';

import { Telicon } from '../../GeneralComponents/Telicon';
import { Label } from '../Label';
import type { InputFocusEvent, Props } from './types';
import './text-input.scss';

const parseAlphanumericInput = (value: string) =>
  value.replace(/[^0-9a-zA-Z]/g, '').replace(/(\..*)\./g, '$1');

const parseLettersOnlyInput = (value: string) =>
  value.replace(/[^a-zA-Z]/g, '').replace(/(\..*)\./g, '$1');

const formatInputValue = (
  value: string,
  maxLength?: number,
  alphanumericOnly?: boolean,
  lettersOnly?: boolean,
  filterFn?: (value: string) => string
) => {
  let formattedValue = value || '';

  if (maxLength) {
    formattedValue = formattedValue.slice(0, maxLength);
  }

  if (alphanumericOnly) {
    formattedValue = parseAlphanumericInput(formattedValue);
  } else if (lettersOnly) {
    formattedValue = parseLettersOnlyInput(formattedValue);
  }

  if (filterFn) {
    formattedValue = filterFn(formattedValue);
  }

  return formattedValue;
};

const TextInput = (
  {
    value: controlledValue,
    className,
    id,
    name = '',
    placeholder,
    label = '',
    info,
    helptext,
    error = false,
    errorMessage,
    readOnly = false,
    disabled = false,
    maxLength,
    lettersOnly = false,
    alphanumericOnly = false,
    customFilter,
    onChange,
    onBlur,
    onFocus,
  }: Props,
  ref: any
) => {
  const isInteractivityDisabled = disabled || readOnly;
  const [innerValue, setInnerValue] = useState('');
  const value = controlledValue || innerValue;
  const formattedValue = formatInputValue(
    value,
    maxLength,
    alphanumericOnly,
    lettersOnly,
    customFilter
  );

  const onChangeEvent = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = event.target.value;
    setInnerValue(newValue);

    if (!onChange || isInteractivityDisabled) return;

    const newFormattedValue = formatInputValue(
      newValue,
      maxLength,
      alphanumericOnly,
      lettersOnly,
      customFilter
    );

    onChange({
      label,
      name,
      id,
      value: newFormattedValue,
      event,
    });
  };

  const onBlurEvent = (event: InputFocusEvent<HTMLInputElement>) => {
    if (!onBlur || isInteractivityDisabled) return;

    onBlur({
      label,
      name,
      id,
      value: event.target.value,
      event,
    });
  };

  const onFocusEvent = (event: InputFocusEvent<HTMLInputElement>) => {
    if (!onFocus || isInteractivityDisabled) return;

    onFocus({
      label,
      name,
      id,
      value: event.target.value,
      event,
    });
  };

  return (
    <div
      className={classNames('sds_TextInput', {
        sds_TextInput_Error: error,
        sds_TextInput_ReadOnly: readOnly,
        sds_TextInput_Disabled: disabled,
        sds_TextInput_InternalIcon: readOnly || error,
        [String(className)]: className,
      })}>
      {label && (
        <Label info={info} htmlFor={id}>
          {label}
        </Label>
      )}
      <div className="sds_TextInput_InputBox">
        <input
          ref={ref}
          id={id}
          name={name}
          {...(!readOnly && !disabled && { id })}
          type="text"
          className="sds_TextInput_Input"
          value={formattedValue}
          placeholder={placeholder}
          readOnly={readOnly}
          disabled={disabled}
          onBlur={onBlurEvent}
          onFocus={onFocusEvent}
          onChange={onChangeEvent}
        />
        {error && !readOnly && (
          <Telicon name="warning--octagon" size="Default" />
        )}
        {readOnly && !error && <Telicon name="lock" size="Default" />}
      </div>
      {error && errorMessage !== '' ? (
        <label className="sds_TextInput_HelpText">{errorMessage}</label>
      ) : (
        helptext && <label className="sds_TextInput_HelpText">{helptext}</label>
      )}
    </div>
  );
};

export default React.forwardRef(TextInput);
