import React, { useState } from 'react';

import classNames from 'classnames';

import { useUpdate } from '../../hooks';
import type { Props } from './types';
import './checkbox-raw.scss';

const CheckboxRaw = (
  {
    className,
    inputClassName,
    value,
    id,
    name,
    checked,
    indeterminate,
    error,
    disabled,
    onChange,
  }: Props,
  ref: any
) => {
  const [innerCheck, setInnerCheck] = useState(
    checked === undefined || checked === null ? false : checked
  );

  const onChangeEvent = (e: any) => {
    setInnerCheck(e.target.checked);

    onChange &&
      onChange({
        id,
        name,
        value,
        checked: e.target.checked,
      });
  };

  useUpdate(() => {
    setInnerCheck(checked === undefined || checked === null ? false : checked);

    onChange &&
      onChange({
        id,
        name,
        value,
        innerCheck,
      });
  }, [checked]);

  return (
    <>
      <input
        type="checkbox"
        className={classNames('sds_CheckboxRaw_Input', {
          [String(inputClassName)]: inputClassName,
        })}
        id={id}
        value={value}
        name={name}
        checked={innerCheck}
        onChange={onChangeEvent}
        ref={ref}
      />
      <label
        className={classNames('sds_CheckboxRaw', {
          sds_CheckboxRaw_Indeterminate: indeterminate,
          sds_CheckboxRaw_Error: error,
          sds_CheckboxRaw_Disabled: disabled,
          [String(className)]: className,
        })}
        htmlFor={disabled ? '' : id}
      />
    </>
  );
};

export default React.forwardRef(CheckboxRaw);
