import React from 'react';

import classNames from 'classnames';

import { Telicon } from '../Telicon';
import type { AvailableTelicon } from '../Telicon/telicon-types';
import { Tooltip } from '../Tooltip';
import type { Props } from './types';
import './avatar.scss';

const Avatar = ({
  className,
  size = 'Default',
  status,
  altStatusText,
  profilePic,
  children,
  tooltip,
}: Props) => {
  const colors = [
    'Neutral',
    'Blue',
    'Green',
    'Yellow',
    'Red',
    'Indigo',
    'Purple',
    'Teal',
    'Lime',
    'Orange',
    'Pink',
  ];

  const gettingInitials = () => {
    if (children) {
      const usernameArray: string[] = children.split(' ');

      if (usernameArray.length > 1) {
        const initials = `${usernameArray[0].substring(
          0,
          1
        )}${usernameArray[1].substring(0, 1)}`;
        return initials.toUpperCase();
      }
      const initials = `${children.substring(0, 2)}`;
      return initials.toUpperCase();
    }
    return 'NaN';
  };

  const getIcon = (): AvailableTelicon => {
    switch (status) {
      case 'Online':
        return 'check';
      case 'Away':
        return 'clock-hands';
      case 'Busy':
        return 'minus';
      case 'Ringing':
        return 'bell';
      case 'OnCall':
        return 'phone';
      case 'OnExternalCall':
        return 'phone-outbound';
      case 'Recovery':
        return 'clock-hands';
      case 'Offline':
        return 'x';
      default:
        return '' as AvailableTelicon;
    }
  };

  const getBackgroundColor = (content: string, colorsArray: string[]) => {
    if (detectUserType() === 'phone') {
      const cleanString = content.replace('+', '').replaceAll(' ', '');
      let addition = 0;
      for (let i = 0; i <= cleanString.length; i++) {
        addition += Number(cleanString.charAt(i));
      }
      const index = addition % colorsArray.length;
      return colorsArray[index];
    }
    const index = String(content).length % colorsArray.length;
    return colorsArray[index];
  };

  const detectUserType = () => {
    const detectString = /[a-z]/g;
    const cleanString = children.replace('+', '');

    if (detectString.test(cleanString)) {
      return 'user';
    }
    return 'phone';
  };

  if (tooltip) {
    return (
      <Tooltip
        message={
          status && status !== 'None'
            ? `${children} <span class='separator'>|</span>  <div class='sds_Tooltip_Status sds_Tooltip_Status_${status}'></div>${
              altStatusText || status
            }`
            : children
        }
        position="Top">
        <div
          className={classNames('sds_Avatar', {
            [`sds_Avatar_${size}`]: size,
            [`sds_Avatar_Color_${getBackgroundColor(children, colors)}`]:
              children,
            [String(className)]: className,
          })}>
          {detectUserType() === 'phone' ? (
            <Telicon name="phone" />
          ) : (
            gettingInitials()
          )}
          <div
            className="sds_Avatar_ProfilePic"
            style={{ background: `url(${profilePic})` }}
          />
          {status && status !== 'None' && (
            <div
              className={classNames('sds_Avatar_Status', {
                [`sds_Avatar_Status${status}`]: status,
              })}>
              <Telicon name={getIcon()} size="Default" />
            </div>
          )}
        </div>
      </Tooltip>
    );
  }
  return (
    <div
      className={classNames('sds_Avatar', {
        [`sds_Avatar_${size}`]: size,
        [`sds_Avatar_Color_${getBackgroundColor(children, colors)}`]: children,
        [String(className)]: className,
      })}>
      {detectUserType() === 'phone' ? (
        <Telicon name="phone" />
      ) : (
        gettingInitials()
      )}
      <div
        className="sds_Avatar_ProfilePic"
        style={{ background: `url(${profilePic})` }}
      />
      {status && status !== 'None' && (
        <div
          className={classNames('sds_Avatar_Status', {
            [`sds_Avatar_Status${status}`]: status,
          })}>
          <Telicon name={getIcon()} size="Default" />
        </div>
      )}
    </div>
  );
};

export default React.memo(Avatar);
