import React from 'react';

import { TextInput } from '../../TextInput';
import type { Props } from './types';

const CreditCardDate = (
  {
    value = '',
    className,
    placeholder,
    id,
    name = '',
    label = '',
    info,
    helptext,
    error = false,
    errorMessage,
    readOnly = false,
    disabled = false,
    onChange,
    onBlur,
    onFocus,
  }: Props,
  ref: any
) => {
  const customFilter = (e: string) => {
    const newValue = e.replace(/[^0-9]/g, '').replace(/(\..*)\./g, '$1');

    const array = newValue.split('');

    if (array.length > 2) {
      array.splice(2, 0, '/');
    }

    let newString = '';
    array.forEach((letter: string) => {
      newString += letter;
    });

    return newString;
  };

  return (
    <TextInput
      value={String(value)}
      id={id}
      className={className}
      name={name}
      label={label}
      placeholder={placeholder}
      info={info}
      customFilter={customFilter}
      helptext={helptext}
      error={error}
      errorMessage={errorMessage}
      readOnly={readOnly}
      disabled={disabled}
      onChange={onChange}
      onBlur={onBlur}
      onFocus={onFocus}
      maxLength={5}
      ref={ref}
    />
  );
};

export default React.forwardRef(CreditCardDate);
